import React, {useEffect, useState} from 'react'
import {useDispatch, useSelector} from "react-redux";
import { DateRangePicker } from 'react-date-range';
import {Dropdown, DropdownToggle, DropdownMenu} from 'reactstrap'
import {

    isSameDay,

    subDays,
    subMinutes,
    subHours
} from 'date-fns';
import {changeDateSettings} from "../../../redux/actions/dashboardActions";
import  moment from 'moment'
const staticRangeHandler = {
    range: {},
    isSelected(range) {
        const definedRange = this.range();
        return (
            isSameDay(range.startDate, definedRange.startDate) &&
            isSameDay(range.endDate, definedRange.endDate)
        );
    },
};

export function createStaticRanges(ranges) {
    return ranges.map(range => ({ ...staticRangeHandler, ...range }));
}


export const defaultStaticRanges = createStaticRanges([
    {
        label: 'Last 5 Minutes',
        range: () => ({
            startDate: subMinutes(new Date(),5 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 15 Minutes',
        range: () => ({
            startDate: subMinutes(new Date(),15 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 30 Minutes',
        range: () => ({
            startDate: subMinutes(new Date(),30 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 1 Hour',
        range: () => ({
            startDate: subMinutes(new Date(),60 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 4 Hour',
        range: () => ({
            startDate: subHours(new Date(),4 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 24 Hour',
        range: () => ({
            startDate: subHours(new Date(),24 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 2 Days',
        range: () => ({
            startDate: subDays(new Date(),2 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 7 Days',
        range: () => ({
            startDate: subDays(new Date(),7 ),
            endDate:new Date()
        }),
    },
    {
        label: 'Last 30 Days',
        range: () => ({
            startDate: subDays(new Date(),30 ),
            endDate:new Date()
        }),
    },

]);
const DateRangeFilter = () =>{

    const currentData = useSelector((state) => state.dashboards.date);
    const [timeRangeDropDown, setTimeRangeDropDown] = useState(false);
    const dispatch = useDispatch();
    const toggle1 = () => setTimeRangeDropDown(prevState => !prevState);
    const [label,setLabel] = useState("")
    const changeDateHandler =  (start, end,label)=>{

        const lable = moment(start.selection.startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(start.selection.endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
        setLabel(lable)
        dispatch(changeDateSettings(start))
    }

    useEffect(() => {
        if(currentData.length!=0){
            const lable = moment(currentData[0].startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(currentData[0].endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
            setLabel(lable)
        }

    },[currentData]);

    return (<Dropdown isOpen={timeRangeDropDown} toggle={toggle1}>
        <DropdownToggle caret>
            {label}
        </DropdownToggle>
        <DropdownMenu style = {{width:"600px",marginLeft: '-85%',top:'35px'}}>
            <DateRangePicker
                onChange={(item,label) => changeDateHandler(item,label)}
                showSelectionPreview={true}
                moveRangeOnFirstSelection={false}

                ranges={currentData}
                direction="vertical"
                scroll={{ enabled: true }}
                staticRanges={defaultStaticRanges}
            />
        </DropdownMenu>
    </Dropdown>)
}

export default DateRangeFilter;