import React,{useState} from 'react';
import {
    Card,
      CardBody, Nav, NavItem, NavLink, TabContent, TabPane,CardTitle,Col,Row,CardText
  } from 'reactstrap';
import '../../../../node_modules/react-splitter-layout/lib/index.css';
import Moment from 'react-moment';
import moment from 'moment'
import { useDispatch, useSelector } from 'react-redux';
import * as widgetHelper  from "../../../shared/helpers/widget_helper"
import Collapse from '../../../shared/components/Collapse';
import DateRangePicker from 'react-bootstrap-daterangepicker'
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-daterangepicker/daterangepicker.css';
const CaseEntityView = (props) =>{

    const evidenceDetails = useSelector((state) => state.cases.evidenceDetails);
    const {fields} = props;
    
    const evidences = [];
    if(evidenceDetails && evidenceDetails.entities){

        for (const [key, value] of Object.entries(evidenceDetails.entities)) {
            let object = new Object();
            object['image'] = widgetHelper.entityTypeMap[fields[key]];  //fields[widgetHelper[evidenceDetails.alertEvidences.entities[i]]];
            object['field'] = value;
            evidences.push(object);
          }

       
    }
    

    return (<div className = "context-details-container">
            <div className = "context-details-container__title">
                <div className = "icon-box icon smp-Alerts">
                         <span class="lnr lnr-flag"></span> 
                </div>

                <p>Alert</p>
                <span className = "description">{evidenceDetails.alertName}</span>      

            </div>





        <Collapse title="Alert Entites" className="with-shadow">

            {evidences.map((value, index) => {

                        return (
                            <div className = "inbox__list-button">
                            <div className = "inbox__mailbox">
                                <img className = "imgContainer" src = {value.image}/> <span className = "entityUser u-ellipsis">{value.field}</span> 
                            </div>

                        </div>
                        )

            })}
                        
                      

            </Collapse> 
            <Collapse title="Alert Details" className="with-shadow">

             <div className = "ui-table-scrollable-wrapper ng-star-inserted">
                 <div className = "ui-table-scrollable-view">
                    <table className = "ui-table table">
                        <thead>
                            <tr>
                                <th>Field Name</th>
                                <th>Value</th>
                            </tr>
                        </thead>
                        <tbody>
                            

                            {evidenceDetails && Object.keys(evidenceDetails).filter(key => key!=='entities').map((value, index) => {
                              
                              return (<tr>
                                <td>{value}</td>
                                  {evidenceDetails[value]  &&  <td className = "u-ellipsis">{evidenceDetails[value].toString()}</td>}
                                </tr>)
                            })}
                           
                        </tbody>
                    </table>
                 </div>
            </div>   
                        

            </Collapse> 
            <Collapse title="Threat" className="with-shadow">

            </Collapse> 
            <Collapse title="Case" className="with-shadow">

            <div className = "ui-table-scrollable-wrapper ng-star-inserted">
                 <div className = "ui-table-scrollable-view">
                    <table className = "ui-table table">
                        <thead>
                            <tr>
                                <th>Field Name</th>
                                <th>Value</th>
                            </tr>
                        </thead>
                        <tbody>
                            

                            {evidenceDetails && Object.keys(evidenceDetails).filter(key => key!=='entities').map((value, index) => {
                              
                              return (<tr>
                                <td>{value}</td>
                                  {evidenceDetails[value] &&  <td className = "u-ellipsis">{evidenceDetails[value].toString()}</td>}
                                </tr>)
                            })}
                           
                        </tbody>
                    </table>
                 </div>
            </div>             


            </Collapse> 

    </div>)

}

export default CaseEntityView;