import axiosInstance from '../middleware/Interceptor'


 

function getFields () {
  return axiosInstance.get('/siem-core/widget/get-mapping-fields')
}

function showPanelPreivew (data) {
  return axiosInstance.post('/siem-core/user/panel/show-panel-preview',data)
}

function savePanel(data){
  return axiosInstance.post('/siem-core/user/panel/save-panel',data)
}

function getPanels(){
  return axiosInstance.get('/siem-core/user/panel/')
}


function loadLogviewerData(data){
 
  return axiosInstance.post('/siem-core/widget/load-events',data)

}
function getAllLogFields(){
  return axiosInstance.get('/siem-core/user/logField/getAllLogFields')
}

function getDashboardCategories(){
  return axiosInstance.get('/siem-core/user/panel/dashboard/categories')
}
function saveDashboard(data){
  return axiosInstance.post('/siem-core/user/panel/dashboard-save',data)
}

function getAllDashboards(){
  return axiosInstance.get('/siem-core/widget/load_all_dahsboards')
}
function getAllDashboardsWithCategoris(){
  return axiosInstance.get('/siem-core/widget/dashboard/getAllCategories')
}
function getSingleDashboard(id){
  return axiosInstance.get('/siem-core/user/panel/dashboard/'+id)
  
}
function saveDashboardLayout(data){
  return axiosInstance.post('/siem-core/user/panel/update-dashboard-layout',data)
}

function deleteWidget(id){
  return axiosInstance.delete('/siem-core/widget/delete_widget/'+id)
}

function saveGridLayout(data){
  return axiosInstance.post('/siem-core/user/panel/dashboard/save-layout',data)
}
function getGridLayout(){
  return axiosInstance.get('/siem-core/user/panel/dashboard/load-layout')
}
function getFieldsWithDataTypes(){
  return axiosInstance.get('/siem-core/user/logField/get-fields-with-data-types')
}






const dashboardService = {
    getFields,
    showPanelPreivew,
    savePanel,
    getPanels,
    loadLogviewerData,
    getAllLogFields,
    getDashboardCategories,
    saveDashboard,
    getAllDashboards,
    getAllDashboardsWithCategoris,
    getSingleDashboard,
    saveDashboardLayout,
    deleteWidget,
    saveGridLayout,
    getGridLayout,
    getFieldsWithDataTypes
}

export default dashboardService