
import React from "react";
import {
    Legend,
    Chart,
    Axis,
  
    Area,
    Line,
    Tooltip

  } from "bizcharts";

import _ from 'lodash';

import DataSet from '@antv/data-set';

import {  CardBody } from 'reactstrap';
import {addFilters} from '../../../redux/actions/panelActions'
import { connect } from 'react-redux';

function processTimeSeriesData (data){
    let finalArray = [];
    for(var i=0;i<data.length;i++){

        let dataPoints = data[i].datapoints;
        dataPoints.map(function(singlePoint){
            let object = new Object();
            if(singlePoint[0]){
                object['value'] = singlePoint[0];
            }else{
                object['value'] = 0;
            }
            object['label'] = singlePoint[1];

            object['metric'] = data[i].target
            finalArray.push(object);
        });

    }
    return finalArray;
}



function getComponent(data,config,widht,height,timeFormat) {
 

  let finalData = [];

  let position = "label*value"
  let scale = {
    label: {
      type: "timeCat",
      mask:timeFormat,
      tickCount:getTickCount(widht)
    },
    value:{
      type: config.scale
    }
   
  };

  if(data.length > 0  && !data[0].rows){
      finalData = processTimeSeriesData(data);
      if(config.areaType && config.areaType != 'normal'){
        const currentData = finalData;
       const  dv  = new DataSet.DataView().source(currentData);
        dv.transform({
                type: 'percent',
                field: 'value',
                dimension: 'metric',
                groupBy: ['label'],
                as: 'percent',
        });
        finalData = [];
        finalData = dv.rows;
        position  = "label*percent"
        scale['percent'] = {
          formatter: (value) => {
            value = +value || 0;
            value = +value * 100;
            return `${value.toFixed(2)}%`;
          },
          alias: 'percent(%)',
        }
      }
     
  }
  




  


  




  class RenderChart extends React.Component {

    

    
    

    componentDidMount() {
     
     
    }

    render() {
     
    
      return (
        
        <div style = {{height:'100%'}}>
         
         {finalData.length > 0 && 
         <Chart
         data={finalData}

         height={height} 
         scale={scale}
         autoFit

         
       >
         <Axis
           name="label"
           label = {{autoRotate:false}}
         />
             <Tooltip shared />
             <Area position="label*value" color="metric" />
             <Line position="label*value" color="metric" />
         
       </Chart>
      }
      {data.length > 0  && data[0].rows &&
          <h4>Unable to Graph Data. Only Time series was supported</h4>
      }
      </div>
       
      );
    }
  }
  return RenderChart;
}

class AreaChart extends React.Component {
  render() {
    const { width, height,timeFromat } = this.props
    const RenderChart = getComponent(this.props.data,this.props.chartConfig,width,height,timeFromat);
    return (
      
       <RenderChart />
      
    );
  }
}
function getTickCount(width){
    if(width>1000){
        return 10;
    }
    if(width>500 && width<1000){
        return 8
    }
    if(width>500){
        return 5
    }
}

export default AreaChart;