import React, {useEffect, useState} from "react";
import Collapse from "../../../shared/components/Collapse";
import {Col, Row, Button} from "reactstrap";
import {Field, reduxForm} from "redux-form";
import 'react-toastify/dist/ReactToastify.css';
import { toast,ToastContainer } from 'react-toastify';
import {connect, useDispatch, useSelector} from "react-redux";
import renderSelectField from "../../../shared/components/form/Select";
import renderMultiSelect from "../../../shared/components/form/MultiSelect";
import ruleService from "../../../api/ruleService";
import userService  from "../../../api/userService";
import TagsInput from 'react-tagsinput'
import dashboardService  from "../../../api/dashboardService";
import 'react-tagsinput/react-tagsinput.css'
import {Query, Builder, BasicConfig, Utils as QbUtils} from 'react-awesome-query-builder';
import MaterialConfig from 'react-awesome-query-builder/lib/config/material';
import 'react-awesome-query-builder/lib/css/styles.css';
import 'react-awesome-query-builder/lib/css/compact_styles.css';
import BarLoader from "react-spinners/BarLoader";
import {viewRules} from "../../../redux/actions/ruleActions";
import CreatableSelect from 'react-select/creatable';
import historicalService from "../../../api/histroicalService";

import '../../../../node_modules/react-selectize/dist/index.min.css'
import {setOptions} from "leaflet/dist/leaflet-src.esm";
import {toLegacyResponseData} from "@grafana/data";
const InitialConfig = MaterialConfig;

const config = {
    ...InitialConfig,
   types:{
        ...InitialConfig.types,
       ip: {
           defaultOperator: "between",
           widgets: {
               text: {
                   operators: ["between", "not_between"],
                   widgetProps: {},
                   opProps: {}
               },
               field: {
                   operators: [//unary ops (like `is_empty`) will be excluded anyway, see getWidgetsForFieldOp()
                       "equal", "not_equal", "proximity" //can exclude if you want
                   ]
               }
           }
       },
       text: {
           defaultOperator: "equal",
           widgets: {
               text: {
                   operators: ["equal","not_equal","like","not_like","select_any_in","select_not_any_in","select_any_in_context","select_any_in_not_context","between", "not_between","ip_range","ip_range_not_in","less","less_or_equal","greater","greater_or_equal"],
                   widgetProps: {},
                   opProps: {}
               },
               field: {
                   operators: [//unary ops (like `is_empty`) will be excluded anyway, see getWidgetsForFieldOp()
                       "equal", "not_equal", "proximity" //can exclude if you want
                   ]
               }
           }
       }
   },
    widgets:{
        ...InitialConfig.widgets,

        ip: {
            ...InitialConfig.widgets.text,
            valueLabel: "String",
            valuePlaceholder: "Enter Prasanth",
        },
    },
    operators:{
        ...InitialConfig.operators,
        equal:{
            ...InitialConfig.operators.equal,
            sqlOp: "equal",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return "".concat(field, ".equals ( ").concat(values).concat(" )");
                } else return undefined; // not supported

            },
        },
        less: {
            label: "<",
            labelForFormat: "<",
            sqlOp: "<",
            reversedOp: "greater_or_equal",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return  "".concat("ConditionExpression.less ( ").concat(field).concat(", ").concat(values).concat(") ");
                } else return undefined; // not supported

            },
        },
        less_or_equal: {
            label: "<=",
            labelForFormat: "<=",
            sqlOp: "<=",
            reversedOp: "greater",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return  "".concat("ConditionExpression.lessThanOrEqual ( ").concat(field).concat(", ").concat(values).concat(") ");
                } else return undefined; // not supported

            },
        },
        greater: {
            label: ">",
            labelForFormat: ">",
            sqlOp: ">",
            reversedOp: "less_or_equal",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return  "".concat("ConditionExpression.greater ( ").concat(field).concat(", ").concat(values).concat(") ");
                } else return undefined; // not supported

            },
        },
        greater_or_equal: {
            label: ">=",
            labelForFormat: ">=",
            sqlOp: ">=",
            reversedOp: "less",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return  "".concat("ConditionExpression.greaterThanOrEqual ( ").concat(field).concat(", ").concat(values).concat(") ");
                } else return undefined; // not supported

            },
        },
        not_equal: {
            ...InitialConfig.operators.not_equal,
            labelForFormat: "!=",
            sqlOp: "<>",
            reversedOp: "equals",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return "!".concat(field, ".equal ( ").concat(values).concat(" )");
                } else return undefined; // not supported

            },

        },
        like:{
            ...InitialConfig.operators.like,
            sqlOp: "",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return  "".concat("ConditionExpression.containsInCondition ( ").concat(field).concat(", ").concat(values).concat(") ");

                } else return undefined; // not supported

            },
        },
        not_like:{
            ...InitialConfig.operators.not_like,
            sqlOp: "",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return  "".concat("ConditionExpression.notContainsInCondition ( ").concat(field).concat(", ").concat(values).concat(") ");


                } else return undefined; // not supported

            },
        },
        starts_with:{
            ...InitialConfig.operators.starts_with,
            sqlOp: "",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return "!".concat(field, ".startsWith ( ").concat(values).concat(" )");
                } else return undefined; // not supported

            },
        },
        ends_with:{
            ...InitialConfig.operators.ends_with,
            sqlOp: "",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return "!".concat(field, ".endsWith ( ").concat(values).concat(" )");
                } else return undefined; // not supported

            },
        },
        select_any_in:{
            ...InitialConfig.operators.select_any_in,
            sqlOp: "",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                if (valueSrc == "value") {
                    return "".concat("ConditionExpression.in_condition ( ").concat(field).concat(", ").concat(values).concat(") ");
                } else return undefined; // not supported else return undefined; // not supported

            },
        },
        select_not_any_in:{
            ...InitialConfig.operators.select_not_any_in,
            sqlOp: "",
            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {

                if (valueSrc == "value") {
                    return "".concat("ConditionExpression.isFoundinContext ( ").concat(field).concat(", ").concat(values).concat(") ");
                } else return undefined; // not supported else return undefined; // not supported

            },
        },
        select_any_in_context: {
            label: "In Context",
            labelForFormat: "In Context",
            sqlOp: "NOT IN",

            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                return "".concat("ConditionExpression.isFoundinContext ( ").concat(field).concat(", '").concat(field).concat("'").concat(", ").concat(values).concat(") ");
            },

            reversedOp: "select_any_in_not_context"
        },
        select_any_in_not_context: {
            label: "Not In Context",
            labelForFormat: "Not In Context",
            sqlOp: "NOT IN",

            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                return "".concat("ConditionExpression.isNotFoundinContext ( ").concat(field).concat(", '").concat(field).concat("'").concat(", ").concat(values).concat(") ");
            },

            reversedOp: "select_any_in_context"
        },
        ip_range: {
            label: "IP Range",
            labelForFormat: "IP Range",
            sqlOp: "NOT IN",
            cardinality: 1,

            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {

                return "".concat("ConditionExpression.ipRange ( ").concat(field).concat(", ").concat(values).concat(") ");
            },

            reversedOp: "ip_range_not_in"
        },
        ip_range_not_in: {
            label: "IP Range Not In",
            labelForFormat: "IP Range Not In",
            sqlOp: "NOT IN",

            sqlFormatOp: function sqlFormatOp(field, op, values, valueSrc, valueType, opDef, operatorOptions) {
                return "".concat("!ConditionExpression.notInIPRange ( ").concat(field).concat(", ").concat(values).concat(") ");
            },

            reversedOp: "select_any_in_context"
        },
    },

    fields: {
        userName: {
            label: 'userName',
            type:'ip',

            valueSources: ['value'],


        },
        qty: {
            label: 'Qty',
            type: 'number',
            fieldSettings: {
                min: 0,
            },
            valueSources: ['value'],
            preferWidgets: ['number'],
        },
        price: {
            label: 'Price',
            type: 'number',
            valueSources: ['value'],
            fieldSettings: {
                min: 10,
                max: 100,
            },

        },
        color: {
            label: 'Color',
            type: 'select',
            valueSources: ['value'],
            fieldSettings: {
                listValues: [
                    {value: 'yellow', title: 'Yellow'},
                    {value: 'green', title: 'Green'},
                    {value: 'orange', title: 'Orange'}
                ],
            }
        },
        is_promotion: {
            label: 'Promo?',
            type: 'boolean',
            operators: ['equal'],
            valueSources: ['value'],
        },
    },

};
const queryValue = {"id": QbUtils.uuid(), "type": "group"};
let CreateRuleBody = (props) =>{
    const { handleSubmit, pristine, reset, submitting,initialValues } = props
    const [loading,setLoading] = useState(false);
    const [tags,setTags] = useState([])
    const [tagValue,setTagValue] = useState([]);
    const [categories , setCategories] = useState([])
    const [users,setUsers] = useState([]);
    const [tatics , setTatics] = useState([])
    const [tree,setTree] = useState(QbUtils.checkTree(QbUtils.loadTree(queryValue), config))
    const [joins,setJoins] = useState([]);
    const [selectLoading,setSelectLoading] = useState(false);
    const [lookupDetails,setLookupDetails] = useState([]);
    const [threatHuntingQuries,setThreatHuntingQuries] = useState([]);
    const dispatch = useDispatch();



    const renderBuilder = (props) => (
        <div className="query-builder-container" style={{padding: '10px'}}>
            <div className="query-builder qb-lite">
                <Builder {...props} />
            </div>
        </div>
    )

    useEffect(()=>{

        fetchAllCategories();
        fetchTactics();
        fetchAllFields()
        fetchAllUsers();
        fetchTags();
        fetchLookupDetails();
        fetchThreatHuntingQuries();

    },[])

    const fetchThreatHuntingQuries = () =>{

        historicalService.getThreatHuntingSaveQuries().then((responseData) =>{
            if(responseData.status === 200){
                let data = responseData.data.map((query)=>{
                    return {label:query.queryName,value:query.queryName}
                })
                setThreatHuntingQuries(data);
            }
        })

    }

    const fetchLookupDetails = () =>{
        ruleService.getLookupDetails().then((responseData) =>{
            if(responseData.status === 200){
                const tempData = responseData.data.map((value)=>{
                    return {value:value.id.toString(),label:value.referenceSetName}
                })

                if(initialValues && initialValues.lookupContext){
                    for(var i=0;i<tempData.length;i++){
                        if(initialValues.lookupContext === tempData[i].value ){
                            initialValues['lookupContext'] = tempData[i];
                        }

                    }

                }

                setLookupDetails(tempData);
            }
        })
    }

    const fetchTags = () =>{
        ruleService.getTags().then((responseData) =>{
            let tags =  responseData.data.map((tag)=>{
                return {label:tag.tagName,value:tag.tagName};
            })
            setTags(tags);
        })
    }

    const fetchAllUsers = ()=>{
        userService.getAllUsers().then((responseData)=>{

            let usernames =  responseData.data.map((value)=>{
                return {label:value.userName,value:value.userName}
            })
            setUsers(usernames)


        })
    }



    const fetchAllEvents = () =>{

        ruleService.getAllEvents().then((responseData)=>{

            dispatch(viewRules(responseData.data.custom));

        })
    }

    const fetchAllFields = () =>{
        dashboardService.getFieldsWithDataTypes().then((responseData) =>{
           let data = responseData.data;
           const object = new Object();
           let keys = Object.keys(data).sort();

           for(var i=0;i<keys.length;i++){
               object[keys[i].replace(".keyword","")] = {label:keys[i].replace(".keyword",""),type:'text'}
           }
           config['fields'] = object;

            if(initialValues && initialValues.eventTags){
                setTagValue(initialValues.eventTags)
                const tempJoins = [];
                for(let i=0;i<initialValues.eventMetaData.length;i++){
                    tempJoins.push({
                        currentJoin:initialValues.eventMetaData[i].currentJoin,
                        previousJoin:initialValues.eventMetaData[i].previousJoin,
                        tree:  QbUtils.checkTree(QbUtils.loadTree(initialValues.eventMetaData[i].tree), config),
                        query:initialValues.eventMetaData[i].query
                    })
                }
                setJoins(tempJoins);
            }
        })
    }


    const fetchAllCategories =()=>{
        ruleService.getAllCategories().then((responseData)=>{

           let categories =  responseData.data.map((value)=>{
                return {label:value.groupName,value:value.groupName}
            })
            setCategories(categories)
        })
    }
    const fetchTactics = () =>{
        ruleService.getTatics().then((responseData)=>{

            let categories =  responseData.data.map((value)=>{
                return {label:value.tactic+" - "+value.techniqueName,value:value.tactic+" - "+value.techniqueName}
            })
            setTatics(categories)
        })
    }
    const addCondition = () =>{
        setJoins( arr => [...arr, {currentJoin:'',previousJoin:'',tree:QbUtils.checkTree(QbUtils.loadTree(queryValue), config)}]);

    }
    const onChange = (immutableTree, config,index) => {

        const newItems = [...joins];
        newItems[index].tree = QbUtils.checkTree(QbUtils.loadTree(immutableTree), config);
        newItems[index]['query'] = JSON.stringify(QbUtils.sqlFormat(immutableTree, config))
        setJoins(newItems)


        console.log(joins);
        // `jsonTree` can be saved to backend, and later loaded to `queryValue`
    }

    const deleteJoin = (index) =>{
        const newItems = [...joins];
        newItems.splice(index,1)

        setJoins(newItems)
    }
    const submit = (values) =>{
        if(!values.eventName ){
            showError("Please Enter Valid Event name")
            return false;
        }
        if(!values.severity ){
            showError("Please Select Severity")
            return false;
        }
        if(!values.ruleGroup ){
            showError("Please Select Group")
            return false;
        }
        if(!values.techniques ){
            showError("Please Select Techniques")
            return false;
        }
        if(!values.reference ){
            showError("Please Select Reference")
            return false;
        }
        if(tags.length===0){
            showError("Please Select Tags")
            return false;
        }
        if(joins.length===0){
            showError("Please Select At least one Event Correlation")
            return false;
        }

        if(!values.triggerCondition){
            showError("Please Select Number of Alerts in Notifications")
            return false;
        }
        if(!values.triggerConditionOperator){
            showError("Please Select Operator in Notifications")
            return false;
        }
        if(!values.triggerConditionValue){
            showError("Please Enter Valid Value")
            return false;
        }
        if(!values.to){
            showError("Please Enter To Email Address In Email Section")
            return false;
        }
        if(!values.subject){
            showError("Please Enter Subject In Email Section")
            return false;
        }
        if(!values.message){
            showError("Please Enter Message In Email Section")
            return false;
        }

        if(!values.title){
            showError("Please Enter Title In Ticket Section")
            return false;
        }
        if(!values.description){
            showError("Please Enter Description In Ticket Section")
            return false;
        }
        if(!values.priority){
            showError("Please Select Priority In Ticket Section")
            return false;
        }
        if(!values.type){
            showError("Please Select Type In Ticket Section")
            return false;
        }
        if(!values.assignee){
            showError("Please Select Assingee In Ticket Section")
            return false;
        }



        const notifications = [
            {
             name:'email',
             to:values.to,
             subject:values.subject,
             message:values.message

            },
            {
                name:'ticket',
                title:values.title,
                description:values.description,
                priority:values.priority,
                assignee:values.assignee,
                type:values.type,

            },
            {
                name:'lookupTable',
                lookupContext:values.lookupContext.value.toString(),


            }

        ];

        let temptatic = values.techniques.map((value)=>{
            return value.value;
        })

        let alert = {triggerCondition:values.triggerCondition.value,triggerConditionOperator:values.triggerConditionOperator.value,triggerConditionValue:values.triggerConditionValue}

        let tempTags =  tagValue.map((value)=>{
            return value.value;
        })

        const submittedData = {
            approvalStatus:'Custom',
            eventTags:tempTags.join(","),
            reference:values.reference.value,
            ruleType:'Rule',
            severity:values.severity.value,
            tatic:temptatic.join(','),
            technique:temptatic.join(','),
            techniques:temptatic.join(','),
            eventType:"Complex Event",
            eventStatus:"enabled",
            eventName:values.eventName,
            eventDescription:values.description,
            eventMetaData:JSON.stringify(joins),
            groupName:values.ruleGroup.value,
            eventAlert:JSON.stringify(alert),
            eventAlertNotification:JSON.stringify(notifications),
            threatHuntingQuery:values.huntingQuery.value
        }
        if(initialValues && initialValues.id){
            submittedData['id']=initialValues.id
        }
        setLoading(true)
       ruleService.createRule(submittedData).then((responseData) =>{
           setLoading(false)
           if(responseData.status===201){
               fetchAllEvents();
               showSuccess('Successfully saved the data');
           }else{
               showError('Unable to Save the data!!');
           }
       })
    }

    const showError = (message) =>{
        toast.error(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }
    const showSuccess = (message) =>{
        toast.success(message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        });
    }

    const handleChangeTag = (newValue,actionMeta) => {

        setTagValue(newValue);
    };

    const customStyles = {
        container: provided => ({
            ...provided,
            width: '100%'
        })
    };

    const createOption = (label) => ({
        label,
        value: label.toLowerCase().replace(/\W/g, ''),
    });

    const handleCreate = (inputValue) =>{
        setSelectLoading(true);

        let data = {id: 0, tagName: inputValue}

        ruleService.createTag(data).then((responseData)=>{
            setSelectLoading(false);
            if(responseData.status===201){
                const newOption = createOption(inputValue);
                tagValue.push(newOption)
                tags.push(newOption)
                setTags(tags)
                setTagValue(tagValue)

            }
        })

    }



    return (
        <React.Fragment>
            {loading &&  <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
            <ToastContainer
                position="top-right"
                hideProgressBar={false}
                autoClose={false}
                newestOnTop={true}
                closeOnClick={false}
                draggable={false}
                rtl={false}
            />

            <form className="form" onSubmit={handleSubmit(submit)} style={{display:'revert'}}>

                <Collapse title="Rule Information" className="boxed">
                    <Row>
                        <Col xs = {12} lg={6} md = {6} sm={12}>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Event Name</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="eventName"
                                        component="input"
                                        type="text"

                                    />
                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Severity</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="severity"
                                        component={renderSelectField}
                                        options={[
                                            { value: 'Critical', label: 'Critical' },
                                            { value: 'High', label: 'High' },
                                            { value: 'Medium', label: 'Medium' },
                                            { value: 'Low', label: 'Low' },
                                        ]}
                                    />
                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Group</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="ruleGroup"
                                        component={renderSelectField}
                                        options={categories}
                                    />
                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Techniques</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="techniques"
                                        component={renderMultiSelect}
                                        options={tatics}
                                    />
                                </div>
                            </div>
                        </Col>
                        <Col xs = {12} lg={6} md = {6} sm={12}>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Description</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="eventDescription"
                                        component="input"
                                        type="text"
                                        placeholder="Event Description"
                                    />
                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Tags</span>
                                <div className="form__form-group-field">
                                    <CreatableSelect styles={customStyles}
                                        isClearable
                                         isMulti
                                        onChange={handleChangeTag}
                                       value = {tagValue}
                                        options={tags}
                                         isDisabled={selectLoading}
                                         isLoading={selectLoading}
                                         onCreateOption={handleCreate}



                                    />

                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Reference</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="reference"
                                        component={renderSelectField}
                                        options={[
                                            { value: 'Mitre', label: 'Mitre' },

                                        ]}
                                    />
                                </div>
                            </div>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Hunting Query</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="huntingQuery"
                                        component={renderSelectField}
                                        options={threatHuntingQuries}
                                    />
                                </div>
                            </div>
                        </Col>
                    </Row>

                </Collapse>
                <Collapse title="Event Correlation" className="boxed">

                    <Button color="primary" onClick={addCondition}>Add</Button>


                    {joins && joins.map((value,index)=>{
                      return (


                          <div> {joins.length && index >= 1 &&
                             <Row>
                                 <Col xs ={12} sm={12} lg={10} md={10} xl={10}>
                                     <table style= {{width:'96%',marginLeft:'28px'}} className="table">
                                         <thead>
                                         <tr>
                                             <th>Current Field</th>
                                             <th>Previous Field</th>
                                         </tr>
                                         </thead>
                                         <tbody>
                                         <tr>
                                             <td><select className="form-control" name="currentField">
                                                 {Object.keys(config.fields).map((key)=>{
                                                     return (  <option value={key}>{key}</option>)
                                                 })}


                                             </select> </td>
                                             <td><select className="form-control" name="previousField">
                                                 {Object.keys(config.fields).map((key)=>{
                                                     return (  <option value={key}>{key}</option>)
                                                 })}

                                             </select> </td>
                                         </tr>
                                         </tbody>

                                     </table>
                                 </Col>
                                 <Col>
                                     <Button color="danger" onClick = { () => deleteJoin(index)}>Delete</Button>
                                 </Col>
                             </Row>

                          } <Query
                          {...config}
                          value={value.tree}
                          onChange={(immutableTree, config) => onChange(immutableTree, config,index)}
                          renderBuilder={renderBuilder}
                      /></div>)
                    })}




                </Collapse>

                <Collapse title="Notifications" className="boxed">
                    <Row>
                        <Col md = {3}>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Trigger alert when *</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="triggerCondition"
                                        component={renderSelectField}
                                        options={[
                                            { value: 'numberOfConditions', label: 'Number of Results' },

                                        ]}
                                    />
                                </div>
                            </div>
                        </Col>
                        <Col md = {3}>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Operator *</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="triggerConditionOperator"
                                        component={renderSelectField}
                                        options={[
                                            { value: 'isGreaterThan', label: 'Is Greater Than' },
                                            { value: 'isLessThan', label: 'Is Less Than' },


                                        ]}
                                    />
                                </div>
                            </div>
                        </Col>
                        <Col md = {3}>
                            <div className="form__form-group">
                                <span className="form__form-group-label">Value *</span>
                                <div className="form__form-group-field">
                                    <Field
                                        name="triggerConditionValue"
                                        component="input"
                                        type = "number"
                                    />
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Collapse  className="with-shadow" title="Email">

                        <Row>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">To *</span>
                                    <div className="form__form-group-field">
                                        <Field
                                            name="to"
                                            component="input"
                                            type = "text"

                                        />
                                    </div>
                                </div>
                            </Col>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Subject</span>
                                    <Field
                                        name="subject"
                                        component="input"
                                        type = "text"

                                    />
                                </div>
                            </Col>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Message *</span>
                                    <div className="form__form-group-field">
                                        <Field
                                            name="message"
                                            component="input"
                                            type = "text"
                                        />
                                    </div>
                                </div>
                            </Col>
                        </Row>

                    </Collapse>
                    <Collapse  className="with-shadow" title="Update Lookup">

                        <Row>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Lookup Name *</span>
                                    <div className="form__form-group-field">


                                        <Field
                                            name="lookupContext"
                                            component={renderSelectField}
                                            options={lookupDetails}
                                        />

                                    </div>
                                </div>
                            </Col>

                        </Row>

                    </Collapse>
                    <Collapse  className="with-shadow" title="Ticket">

                        <Row>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Title *</span>
                                    <div className="form__form-group-field">
                                        <Field
                                            name="title"
                                            component="input"
                                            type = "text"

                                        />
                                    </div>
                                </div>
                            </Col>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Description</span>
                                    <Field
                                        name="description"
                                        component="input"
                                        type = "text"

                                    />
                                </div>
                            </Col>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Priority  *</span>
                                    <div className="form__form-group-field">
                                        <Field
                                            name="priority"
                                            component={renderSelectField}
                                            options={[
                                                { value: 'Critical', label: 'Critical' },
                                                { value: 'High', label: 'High' },
                                                { value: 'Medium', label: 'Medium' },
                                                { value: 'Low', label: 'Low' },

                                            ]}
                                        />
                                    </div>
                                </div>
                            </Col>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Type  *</span>
                                    <div className="form__form-group-field">
                                        <Field
                                            name="type"
                                            component={renderSelectField}
                                            options={[
                                                { value: 'Port Scanning Activity', label: 'Port Scanning Activity' },
                                                { value: 'Malware Infection', label: 'Malware Infection' },
                                                { value: 'Distributed Denial Of Service', label: 'Distributed Denial Of Service' },
                                                { value: 'Distributed Denial Of Service Diversion', label: 'Distributed Denial Of Service Diversion' },
                                                { value: 'Unauthorized Access', label: 'Distributed Denial Of Service Diversion' },
                                                { value: 'Insider Breach', label: 'Insider Breach' },
                                                { value: 'Destructive Attack', label: 'Destructive Attack' },
                                                { value: 'False Alarms', label: 'False Alarms' },
                                                { value: 'Advanced Persistent Threat Or Multistage Attack', label: 'Advanced Persistent Threat Or Multistage Attack' },
                                                { value: 'Other', label: 'Other' },



                                            ]}
                                        />
                                    </div>
                                </div>
                            </Col>
                            <Col md = {3}>
                                <div className="form__form-group">
                                    <span className="form__form-group-label">Assignee  *</span>
                                    <div className="form__form-group-field">
                                        <Field
                                            name="assignee"
                                            component={renderSelectField}
                                            options={users}
                                        />
                                    </div>
                                </div>
                            </Col>
                        </Row>

                    </Collapse>

                </Collapse>
                <div className="">
                    <Button type = "submit" color="primary">Save</Button>
                    <Button type = "button" color="primary">Cancel</Button>
                </div>
            </form>
        </React.Fragment>

    )

}

CreateRuleBody =  reduxForm({
    form: 'rule_form',
    enableReinitialize : true
})(CreateRuleBody)

CreateRuleBody = connect(
    state => ({
        initialValues: state.ruleReducer.formData
    }),

)(CreateRuleBody)

export default CreateRuleBody