import React,{useEffect,useState} from 'react';
import { useDispatch, useSelector,connect } from 'react-redux';
import '../../../../node_modules/react-splitter-layout/lib/index.css';
import {Badge, Button, CardBody, Modal, ModalBody, ModalFooter, ModalHeader} from 'reactstrap';
import {List} from 'react-virtualized';
import caseService from '../../../api/caseService'
import {openCase} from '../../../redux/actions/casesAction'
import { Field, reduxForm } from 'redux-form'
import renderSelectField from "../../../shared/components/form/Select";
import renderMultiSelectField from "../../../shared/components/form/MultiSelect";
import userService from "../../../api/userService";
import moment from "moment";
import DateRangePicker from "react-bootstrap-daterangepicker";
import {changeDateSettings} from "../../../redux/actions/dashboardActions";


const CasesSidePanel = (props) =>{

    const [cases,setCases] = useState([]);

    const [date,setDate] = useState({startDate:0,endDate:0,label:'Last 7 Days'})
    const [activeCase,setActiveCase] = useState({});
    const {users} = props;
    if(props.caseId){
       console.log(props.caseId);
    }


    const changeDateRange = (start, end,label)=>{

        console.log(start)

        setDate({startDate:start.valueOf(),endDate:end.valueOf(),label:label})



    }

    const filterData = (values) =>{
        const formData = {};
        if(values.analyst){
            const tempData = [];
            for(var i=0;i<values.analyst.length;i++){
                tempData.push(values.analyst[i].value);
            }
            formData['users'] = tempData.join(',');
        }
        if(values.priorities){
            const tempData = [];
            for(var i=0;i<values.priorities.length;i++){
                tempData.push(values.priorities[i].value);
            }
            formData['priorities'] = tempData.join(',');
        }
        if(values.state){
            const tempData = [];
            for(var i=0;i<values.state.length;i++){
                tempData.push(values.state[i].value);
            }
            formData['state'] = tempData.join(',');
        }
        formData['startDate'] = date.startDate;
        formData['endDate'] = date.endDate;
        caseService.filterCase(formData).then((response)=>{
            setCases(response.data);
        })
    }

    useEffect(() => {
            
            fetchAllCases()
        }
    ,[]);
    //let currentCase = {};
   const  fetchAllCases = () =>{
        caseService.getAllCases() .then((response) => {
            if(response.status === 200){
                    const tmepData = [];
                    let isFound = false;
                    for(let  i=0;i<response.data.length;i++){
                            if(response.data[i].id === props.caseId){
                                response.data[i].active = true;
                                isFound = true;
                            }
                         tmepData.push( response.data[i]);
                    }
                    if(!isFound){
                        caseService.getSingleCase(props.caseId).then((response) =>{
                            if(response.status===200){

                                const tempData = {
                                    name:response.data.title,
                                    id:response.data.id,
                                    priority:response.data.priority,
                                    createdDate:response.data.createDate,
                                    alertEvidencesCount:response.data.alertEvidences.length,
                                    assignedUser:response.data.assignedUser,
                                    caseId:response.data.caseId,
                                    active:true
                                }

                                tmepData.unshift( tempData);
                                setCases(tmepData);
                            }
                        })

                    }else{
                        setCases(tmepData);
                    }

            }

         })
    }


    

return (
    <div className="cases-content__start">
            <SideBarHeader changeDateRange = {changeDateRange} label = {date.label} filterData = {filterData} users = {users} size = {cases.length}/>
            <div className="left-sidebar__field">
                   <div className = "case-queue-list">
                    
                        <CasesQueue   rows = {cases}/>

                   </div>
            </div>




    </div>

)

}

const CasesQueue = (props) =>{
    const {rows} = props;
    const [open,setOpen] = useState(false)
    const [currentCase,setCurrentCase] = useState("")

    const dispatch = useDispatch();
    const openCurrentCase = (singleCase) =>{
        caseService.getSingleCase(singleCase.id) .then((response) => {
            if(response.status===200 && response.data){
                dispatch(openCase(response.data));
                setCurrentCase(singleCase.id)
            }
         })
     }

     useEffect(()=>{
         if(rows.length>0){
             let currentCase = {};
             for(let i=0;i<rows.length;i++){
                 if(rows[i].active){

                     currentCase = rows[i];

                 }
             }

             if(Object.keys(currentCase).length>0){
                 openCurrentCase(currentCase)
             }else{
                 openCurrentCase(rows[0])

             }

         }else{
             openCurrentCase({})
         }
     },[rows])




    function rowRenderer({ key, index,  isScrolling, isVisible, style, }) {
        return (

            <div className={currentCase === rows[index].id ? "case-card active" : "case-card"} key={key} style={style}>

                        <div className={`status--${rows[index].priority}`}>
                              <div  className={"case-card__left"}>
                                    <div className = "case-card__left--top">
                                        <div class="case-card__name" tnautomationid="caseCardName" onClick={ () => openCurrentCase(rows[index]) }>{rows[index].name} </div>
                                           <div class="case-card__time-id font-regular-secondary">
                                           <span class="ng-star-inserted" style={{fontWeight: "bold"}}>Modified </span>
                                           <span tnautomationid="caseCardTime" class="ng-star-inserted">{rows[index].createdDate}</span>

                                        </div>
                                    </div>

                                    </div>

                                    <div className={currentCase === rows[index].id ? "case-card__right active" : "case-card__right"}>
                                <div className = "queue-case-info-icons">
                                        <div className = "case-card__round-actions">
                                           
                                                   <ul class="case-card__round-actions">
                                                   <li class="ng-star-inserted">
                                                       <Badge color="primary">{rows[index].assignedUser &&  rows[index].assignedUser.substring(0, 2).toLocaleUpperCase()}</Badge>
                                                   </li>
                                                   <li class="ng-star-inserted">
                                                          <Badge color="info">{rows[index].alertEvidencesCount}</Badge>
                                                    </li>
                                                    

                                            </ul>
                                        </div>
                                </div>
                           </div>
                           </div>
                         
                    </div>

            
        );
      }

      return (
          <React.Fragment>

              <List  height={700} width = {250}
                     rowCount={rows.length}
                     rowHeight={110}
                     rowRenderer={rowRenderer}/>

          </React.Fragment>

        
    )


}

let Model = (props) =>{

    const { handleSubmit,label } = props;


    return (

        <Modal isOpen={props.open}  >
        <ModalHeader>Cases Filter</ModalHeader>
            <form onSubmit={handleSubmit(props.filterData)} className="form" >
        <ModalBody>
            <div className="form__form-group">
                <span className="form__form-group-label">Priorities</span>
                <div className="form__form-group-field">
                    <Field
                        name="priorities"
                        component={renderMultiSelectField}
                        options={[
                            { value: 'critical', label: 'Critical' },
                            { value: 'high', label: 'High' },
                            { value: 'medium', label: 'Medium' },
                            { value: 'low', label: 'Low' },
                        ]}
                    />
                </div>
            </div>
            <div className="form__form-group">
                <span className="form__form-group-label">State</span>
                <div className="form__form-group-field">
                    <Field
                        name="state"
                        component={renderMultiSelectField}
                        options={[
                            { value: 'Open', label: 'Open' },
                            { value: 'In progress', label: 'In Progress' },
                            { value: 'Closed', label: 'Close' },
                            { value: 'Resolved', label: 'Resolved' },
                        ]}
                    />
                </div>
            </div>
            <div className="form__form-group">
                <span className="form__form-group-label">Analyst</span>
                <div className="form__form-group-field">
                    <Field
                        name="analyst"
                        component={renderMultiSelectField}
                        options={props.users}
                    />
                </div>
            </div>
            <div className="form__form-group">
                <span className="form__form-group-label">Date Range</span>
                <div className="form__form-group-field">
                <DateRangePicker
                    initialSettings={{

                        ranges: {
                            "Past 5 Minutes": [moment().subtract(5,"minutes").toDate(), moment().toDate()],
                            "Past 15 Minutes": [moment().subtract(15,"minutes").toDate(), moment().toDate()],
                            "Past 30 Minutes": [moment().subtract(30,"minutes").toDate(), moment().toDate()],
                            "Past 1 Hour": [moment().subtract(60,"minutes").toDate(), moment().toDate()],
                            "Past 4 Hour": [moment().subtract(4,"hours").toDate(), moment().toDate()],
                            "Past 1 Day": [moment().subtract(24,"hours").toDate(), moment().toDate()],
                            "Past 2 Days": [moment().subtract(48,"hours").toDate(), moment().toDate()],
                            "Past 7 Days": [moment().subtract(7,"days").toDate(), moment().toDate()],
                            "Past 30 Days": [moment().subtract(30,"days").toDate(), moment().toDate()],
                        },
                    }}
                    onCallback = {props.changeDateRange.bind(this)}
                >
                    <div
                        id="reportrange"
                        className="col-12"
                        style={{

                            cursor: 'pointer',
                            padding: '5px 10px',
                            border: '1px solid #ccc',
                            width: '100%',
                        }}
                    >
                        <i className="fa fa-calendar"></i>&nbsp;
                        <span>{label}</span> <i className="fa fa-caret-down"></i>
                    </div>
                </DateRangePicker></div>
            </div>

        </ModalBody>

        <ModalFooter>
            <Button type="submit" color="primary" >Save</Button>{' '}
            <Button color="secondary" onClick={props.cancel}>Cancel</Button>
        </ModalFooter>
            </form>
    </Modal>);

}

const SideBarHeader = (props) =>{
    const {size,users} = props;
    const [open,setOpen] = useState(false);
    const tempUsres =  [];
    if(users.length>0){
        for(let i=0;i<users.length;i++){
            tempUsres.push({value:users[i].name,label:users[i].name})
        }
    }
    const toggle = () =>{
        setOpen(!open)
    }



    return (
        <React.Fragment>
            <Model changeDateRange = {props.changeDateRange} label = {props.label} filterData = {props.filterData} users = {tempUsres} open = {open} cancel = {toggle}/>
            <div className="left-sidebar__header">
                <div className="head-text"> {size} Cases</div>
                <div className="action-group">
                    <div   className="ob-action">
                        <span className="lnr lnr-plus-circle"></span>
                    </div>
                    <div onClick={toggle} className="ob-action">
                        <span className="lnr lnr-funnel"></span>
                    </div>
                    <div className="ob-action">
                        <span className="lnr lnr-sync"></span>
                    </div>


                </div>
            </div>


        </React.Fragment>
        )

}

Model = reduxForm({

    form: 'caseFilterForm'
})(Model)


export default connect()(CasesSidePanel)
