import React, { useState, useEffect } from 'react';

import { useDispatch, useSelector } from 'react-redux';
import {  Card,CardBody } from 'reactstrap';
import dashboardService from '../../../api/dashboardService'
import Tree from '@naisutech/react-tree'
import {addDasboard} from '../../../redux/actions/dashboardLayoutActions'


const DashboardTree = (props) =>{

    const [treeData,setTreeData] = useState([]);

    const dispatch = useDispatch();

    const addThisDashboardToLayout = (event) =>{
        if(event && event.type === "Dashboard"){
            const currentDashboard = {
                "type": "tab",
                "id": "#"+event.id,
                "name": event.label,
                "component": "dashboard"
            }
            props.addDashboardToLayout(currentDashboard);
            //dispatch(addDasboard(currentDashboard));
        }
        
    }   

    useEffect(() => {
        fetchTreeData();
      }, []);
   
   const  fetchTreeData = () =>{
        dashboardService.getAllDashboardsWithCategoris().then((response) => {
            setTreeData(response.data)
        });
    }

    return (<Tree onSelect={addThisDashboardToLayout} nodes={treeData} grow theme={'light'} />)


}
export default DashboardTree;
