const getColDefs = (props,type) =>{
    const {fields} = props;
    const fieldsNames = [];
    const columns = [];
    for(var i=0;i<fields.length;i++){
        let field = fields[i];

        if(!fieldsNames.includes(field.label)){
            fieldsNames.push(field.label);
            if(type === 'alerts'){
                columns.push({
                    headerName:field.label.replace(".keyword",""),
                    field:"rule_data.event_data."+field.label.replace(".keyword",""),
                    hide:true,
                    width:150,
                    filter: 'agTextColumnFilter',
                    enableRowGroup: true
                })
            }else{
                columns.push({
                    headerName:field.label.replace(".keyword",""),
                    field:field.label.replace(".keyword",""),
                    hide:true,
                    width:150,
                    filter: 'agTextColumnFilter',
                    enableRowGroup: true
                })
            }

        }
    }


    if(type === 'events'){
        columns.push({headerName:"@timestamp", field: "@timestamp", width: 200,});
        columns.push({headerName:"event_name", field: "event_name",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"event_category", field: "event_category",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"log_type", field: "log_type",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"log_device", field: "log_device",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
    }
    if(type === 'alerts'){
        columns.push({headerName:"@timestamp", field: "@timestamp", width: 200,headerCheckboxSelection: true,  headerCheckboxSelectionFilteredOnly: true,checkboxSelection: true});
        columns.push({headerName:"Alert Name", field: "name", hide:true, width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',rowGroup: true,filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Priority", field: "priority",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Category", field: "rule_category",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Case", field: "case_name", width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Event Name",hide:true,field: "event_name", width: 200,enableRowGroup: false,rowGroup: true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});


        columns.push({headerName:"Assignee",hide:true,field: "assignee", width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});
        columns.push({headerName:"Status",hide:true,field: "alert_status", width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{ filterOptions:['contains'],suppressAndOrCondition:true }});
    }


    return columns;
}

const getDefaultColDef = () =>{
    const def = {
        flex: 1,
        minWidth: 100,
        filter: false,
        sortable: true,
        resizable: true,
        floatingFilter: true
    }
    return def;
}
const getSideBar = () =>{

}


const HistoircalLayout = (props) =>{

    return ( <SplitterLayout primaryIndex={0} percentage = {70} secondaryInitialSize = {30}>

        <HistoricalTable tableMetaData = {props.tableState} type = {props.type}
                         startDate = {props.startDate} endDate = {props.endDate}
                         filters= {props.filters}
                         searchString = {props.searchString}
                         fields = {props.fields}/>

       <DetailedView/>

    </SplitterLayout>)
}

const DetailedView = () =>{
    const {eventData,type} = useSelector((state) => state.historicalReducer.historicalModel);

    const [isModelOpen,setiSModelOpen] = useState(false)

    const openEventView = (event) =>{
        event.preventDefault();
        setiSModelOpen(true);
    }
    const closeEventView = () =>{

        setiSModelOpen(false);
    }
    if(eventData && type === 'events'){
        return (
             <Card>
                <CardBody>

                    <table class = 'table'>
                        <tbody>
                        {
                            Object.keys(eventData).map((key, id) =>
                                <tr key = {id}>
                                    <td className= "td-field-label">{key}</td>
                                    <td>{eventData[key]}</td>
                                </tr>
                            )
                        }
                        </tbody>
                    </table>




                </CardBody>
            </Card>

        )
    }else if(eventData && type === 'alerts'){
        return (
            <Card>
                <CardBody>




                    <table class = 'table'>
                        <tbody>


                        {
                            Object.keys(eventData).filter(key => key!='rule_data').map((key, id) =>
                                <tr key = {id}>
                                    <td className= "td-field-label">{key}</td>
                                    <td>{eventData[key]}</td>
                                </tr>
                            )
                        }
                        {eventData['rule_data'] &&
                         <tr>
                            <td className= "td-field-label">Event Count</td>
                            <td><a href="#" onClick = {openEventView}>{eventData['rule_data']['event_data'].length}</a> </td>
                        </tr>
                        }



                        </tbody>
                    </table>

                    <ModelAlertView  close = {closeEventView} type = {type} eventData={eventData} isModelOpen = {isModelOpen}/>




                </CardBody>
            </Card>
        )
    }else{
        return (<div>No Data</div>)
    }

}

const ModelAlertView = (props) =>{
    const {eventData,isModelOpen,close} = props;

    const [eventInfo,setEventInfo] = useState({})
    const showEventInfo = (data) =>{
        setEventInfo(data)
    }

    useEffect(()=>{
        console.log(eventData)
        if(eventData && eventData['rule_data']){
            setEventInfo(eventData['rule_data']['event_data'][0]);
        }
    })

    return ( <Modal isOpen={isModelOpen} className="add-widget-model" >
        <ModalHeader >Alert View</ModalHeader>
        <ModalBody>

            <form className="form form--horizontal">
                <Row style = {{width:'100%'}}>

                    <Col xs = {12} md = {6} lg={6} xl={6} sm={12}>
                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Timestamp :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['@timestamp']}</span>
                            </div>
                        </div>


                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Description :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['description']}</span>
                            </div>
                        </div>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Category :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['rule_category']}</span>
                            </div>
                        </div>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Tactics :</b></span>
                            <div className="form__form-group-field">


                                {eventData['techniques'] && eventData['tactics'] != 'low' &&
                                <div className="badge badge-success" > {eventData['tactics']}</div>
                                }
                            </div>
                        </div>







                    </Col>

                    <Col xs = {12} md = {6} lg={6} xl={6} sm={12}>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Name :</b></span>
                            <div className="form__form-group-field">
                                <span>{eventData['name']}</span>
                            </div>
                        </div>



                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Priority :</b></span>
                            <div className="form__form-group-field">

                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'high' &&
                                <div className="badge badge-danger" >{eventData['priority']}</div>
                                }
                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'medium' &&
                                <div className="badge badge-warning" >{eventData['priority']}</div>
                                }
                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'critical' &&
                                <div className="badge badge-danger" >{eventData['priority']}</div>
                                }
                                {eventData['priority'] && eventData['priority'].toLowerCase() === 'low' &&
                                <div className="badge badge-success" >{eventData['priority']}</div>
                                }
                            </div>
                        </div>

                        <div className="form__form-group">
                            <span className="form__form-group-label"><b>Techniques :</b></span>
                            <div className="form__form-group-field">


                                {eventData['techniques'] && eventData['techniques'] != 'low' &&
                                     <div className="badge badge-success" > {eventData['techniques']}</div>
                                }
                            </div>
                        </div>





                    </Col>


                </Row>
                <Card>
                <Row>
                    <table className="table table-striped">
                        <thead>
                            <tr>
                                <th>Timestamp</th>
                                <th>Event Name</th>
                                <th>Event Description</th>
                                <th>Event Category</th>
                                <th>Log Device</th>
                            </tr>
                        </thead>
                        <tbody>
                        {eventData && eventData['rule_data'] && eventData['rule_data']['event_data'].map((event,index)=>{
                           return( <tr onClick = {()=>{showEventInfo(eventData['rule_data']['event_data'][index])}} style={{cursor:"pointer"}}>
                                    <td>{event['@timestamp']}</td>
                                    <td>{event['event_name']}</td>
                                    <td>{event['event_description']}</td>
                                    <td>{event['event_category']}</td>
                                    <td>{event['log_device']}</td>
                            </tr>)
                        }) }
                        </tbody>

                    </table>



                    <Card style = {{paddingLeft:'12px'}}>
                        <div className ="card-title" style={{borderBottom:'1px solid #e7e2e2'}}>
                            <h4 style={{textAlign:'left'}}>Event Information</h4>
                        </div>

                         <form className="form form--horizontal">


                        <Row>
                            {
                                Object.keys( eventInfo).map((singleEvent)=>{
                                   return( <Col xs = {12} lg = {6} md = {6} sm={12}>
                                           <div className="form__form-group">
                                               <span className="form__form-group-label" style={{width:'180px'}}><b>{singleEvent} :</b></span>
                                               <div className="form__form-group-field" style={{width: "calc(100% - 202px)"}}>

                                                   {eventInfo[singleEvent]}


                                               </div>
                                           </div>
                                        </Col>
                                   )
                                })

                            }
                        </Row>
                      </form>

                    </Card>

                </Row>
                </Card>

            </form>

        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick={props.close} >Close</Button>{' '}

        </ModalFooter>
    </Modal>)

}

const HistoricalTable = (props) =>{

    const {type,startDate,endDate,filters,searchString,tableMetaData} = props;

    const saveFilters = useSelector((state) => state.historicalReducer.saveFilters);





    const prevCountRef = useRef();
    const prevFilterRef = useRef();
    const constTempFilters = [];
    for(var i=0;i<filters.length;i++){
        constTempFilters.push(filters[i].key);
    }
    useEffect(() => {
        prevCountRef.current =startDate+"_"+constTempFilters.join("_")+"_"+searchString+"_"+type;

    },[startDate,constTempFilters.join("_"),searchString,type]);

    useEffect(() => {

        prevFilterRef.current = saveFilters.filterName;

    },[saveFilters.filterName]);



    //console.log(prevCountRef.current.startDate);


    const [gridApi, setGridApi] = useState(null);
    const [gridColumnApi, setGridColumnApi] = useState(null);
    const cols = getColDefs(props,type)
    const defaultColDef = getDefaultColDef();
    const sideBar = getSideBar();
    const dispatch = useDispatch();
    const inputEl = useRef(null);
    let mergedFilters = [];
    if(type && type === 'alerts' ){
        let tempFilters = [];
        for(let i=0;i<filters.length;i++){
            let key = filters[i].key;
            if(!key.startsWith("rule_data.event_data.")){
                tempFilters.push({key:"rule_data.event_data."+filters[i].key,operator:filters[i].operator,value:filters[i].value})
            }else{
                tempFilters.push(filters[i])
            }
        }

        mergedFilters = tempFilters;
    }else{
        let tempFilters = [];
        for(let i=0;i<filters.length;i++){
            let key = filters[i].key;
            if(key.startsWith("rule_data.event_data.")){
                tempFilters.push({key:filters[i].key.replace("rule_data.event_data.",""),operator:filters[i].operator,value:filters[i].value})
            }else{
                tempFilters.push(filters[i])
            }
        }

        mergedFilters = tempFilters;
    }
    const queryBuilder = new ElasticQueryBuilder({
        timeField: '@timestamp',
        esVersion: '7.9',
    });
    var tempQuery = queryBuilder.build([],mergedFilters,searchString);
    //{"bool":{"must":[]}}
    let finalQuery ={bool:{must:[]}}

    if(tempQuery.query && tempQuery.query.bool.must){
         finalQuery = {bool:{must:tempQuery.query.bool.must}}
    }
    if(searchString!='*'){
        finalQuery.bool['filter'] = [{"query_string":{"query":searchString,"analyze_wildcard":true}}]
    }


    const onGridReady = (params) => {
        setGridApi(params.api);
        setGridColumnApi(params.columnApi);

        var columnToolPanel = params.api.getToolPanelInstance('columns');
        columnToolPanel.setPivotModeSectionVisible(false);
        columnToolPanel.setRowGroupsSectionVisible(false);
        columnToolPanel.setValuesSectionVisible(false);
        params.api.closeToolPanel();
        var datasource =  ServerSideDatasource();
        params.api.setServerSideDatasource(datasource);

    }
    const ServerSideDatasource  = () => {
        return {
            getRows :function(params) {
                console.log(JSON.stringify(params.request, null, 1));
                params.request['startDate'] = startDate
                params.request['endDate'] = endDate
                params.request['type'] = type;
                params.request['query'] = JSON.stringify(finalQuery);
                params.request['queryString'] = searchString;
                params.request['companyName'] = 'technominds';
                dashboardService.loadLogviewerData(params.request).then(response => {
                    dispatch(showSingleHistoricalEvent(response.data.data[0]))
                    params.successCallback(response.data.data, response.data.lastRow);
                })
            }
        }
    };
    const onSelectionChanged = () =>{
        let selectedRows = gridApi.getSelectedRows();
        dispatch(showSingleHistoricalEvent(selectedRows[0]))


    }

    if(gridApi && prevCountRef.current && prevCountRef.current != startDate+"_"+constTempFilters.join("_")+"_"+searchString+"_"+type ){

        if(tableMetaData && tableMetaData.colState){
            gridApi.gridCore.columnApi.setColumnState(tableMetaData.colState);
            gridApi.gridCore.columnApi.setColumnGroupState(tableMetaData.groupState);

            gridApi.gridCore.gridApi.setFilterModel(tableMetaData.filterState);
        }




        gridApi.purgeServerSideCache([]);
        var datasource =  ServerSideDatasource();
        gridApi.setServerSideDatasource(datasource);
    }
    if(gridApi && prevFilterRef.current != saveFilters.filterName){
        var colState = gridApi.gridCore.columnApi.getColumnState();
        var groupState = gridApi.gridCore.columnApi.getColumnGroupState();

        var filterState = gridApi.gridCore.gridApi.getFilterModel();
        var tempData = {'colState':colState,'groupState':groupState,'filterState':filterState};
        let finalData = {id:saveFilters.id,queryName:saveFilters.filterName,tableMetaData:JSON.stringify(tempData),metaData:JSON.stringify(props)}
        histroicalService.saveFilters(finalData).then((responseData) =>{
            console.log(responseData)
        })
    }


    return (
        <div
            ref={inputEl}
            id="myGrid"
            style={{
                height: '100%',
                width: '100%',
            }}
            className="ag-theme-alpine"
        >
            <AgGridReact
                rowHeight={30}
                rowSelection={'single'}

                onSelectionChanged={onSelectionChanged}
                sideBar={{
                    toolPanels: [
                        {
                            id: 'columns',
                            labelDefault: 'Columns',
                            labelKey: 'columns',
                            iconKey: 'columns',
                            toolPanel: 'agColumnsToolPanel',
                            toolPanelParams: {
                                suppressRowGroups: true,
                                suppressValues: true,
                                suppressPivots: true,
                                suppressPivotMode: true,
                                suppressSideButtons: true,
                                suppressColumnFilter: true,
                                suppressColumnSelectAll: true,
                                suppressColumnExpandAll: true,
                            },
                        },
                    ],
                    defaultToolPanel: 'columns',
                }}
                sideBar={true}
                modules={[ServerSideRowModelModule,RowGroupingModule,MenuModule,ColumnsToolPanelModule, MasterDetailModule,MenuModule]}
                columnDefs={cols}
                pivotMode={false}
                rowGroupPanelShow= {'always'}
                defaultColDef={defaultColDef}
                rowModelType={'serverSide'}
                animateRows={true}
                onGridReady={onGridReady}
                rowDataChangeDetectionStrategy={ChangeDetectionStrategyType.NONE}

            /></div> )




}

export default HistoircalLayout;