
import PanelModel from './PanelModel'
import _ from "lodash";


export default   class DashboardModel {

    id:any;
    uid:string;
    editable:any;
    time:any;
    refresh:any;
    panels:PanelMoel[]
    panelInEdit: PanelModel;
    panelInView: PanelModel;
    description:any;
    title:any;
    showCreatePanel:any;
    isEditDashboardOpen:any;
    category:any;
    permissions:any;


    constructor(data){
        this.id = data.id;
        this.uid = data.uid;
        this.title = data.title;
        this.description = data.description
        this.time = data.time;
        this.refresh = data.refresh;
        this.panels =  data.panels;
        this.currentPanel = {};
        this.isSidePanelOpen = false;
        this.showCreatePanel = false;
        this.isEditDashboardOpen = false;
        this.category = data.category;
        this.permissions = data.permissions;

    }

    updateNonSelectValue(event){
      
      if(event.target &&  event.target.name === 'title'){
        this.title = event.target.value;
      }
      if(event.target &&  event.target.name === 'description'){
        this.description = event.target.value;
      }
    
     
    }

    addPanels(panel){
        this.panels = panel;
        //this.panels.push(panel);
    }

    updatePermissions(index,event,name){
      this.permissions[index][name] = event
    }

    updateCategory(event){
      this.category = event.value
    }
    addField (){
      this.permissions.push({});
    }
    removeField(index){
      this.permissions.splice(index,1);
    }

    initViewPanel(panel: PanelModel) {
        this.panelInView = panel;
        panel.setIsViewing(true);
      }
    
    exitViewPanel(panel: PanelModel) {
        this.panelInView = undefined;
        panel.setIsViewing(false);
    }

    getNextPanelId() {
        let max = 0;
    
        for (const panel of this.panels) {
          if (panel.id > max) {
            max = panel.id;
          }
    
          if (panel.collapsed) {
            for (const rowPanel of panel.panels) {
              if (rowPanel.id > max) {
                max = rowPanel.id;
              }
            }
          }
        }
    
        return max + 1;
      }

      getPanelById(id: number): PanelModel | null {
        if (this.panelInEdit && this.panelInEdit.id === id) {
          return this.panelInEdit;
        }
    
        for (const panel of this.panels) {
          if (panel.id === id) {
            return panel;
          }
        }
    
        return null;
      }

      getPannels(){
          return this.panels;
      }

      addPanel(panelData: any) {
        panelData.id = this.getNextPanelId();
    
        const panel = new PanelModel(panelData);
    
        this.panels.unshift(panel);
    
        this.sortPanelsByGridPos();
        this.currentPanel = panel;
       
      }
      getCurrentPanel(){
        return this.currentPanel;
      }
      sortPanelsByGridPos() {
        this.panels.sort((panelA, panelB) => {
          if (panelA.gridOptions.y === panelB.gridOptions.y) {
            return panelA.gridOptions.x - panelB.gridOptions.x;
          } else {
            return panelA.gridOptions.y - panelB.gridOptions.y;
          }
        });
      }


}