import React, {useState} from 'react';

import Graph from "react-graph-vis";
import '../../../../node_modules/@antv/graphin/dist/index.css';
import '../../../../node_modules/@antv/graphin-components/dist/index.css'; // Graphin CSS
import * as widget_helper from '../../../shared/helpers/widget_helper'
import caseService from '../../../api/caseService'
import BarLoader from "react-spinners/BarLoader";
import {useDispatch, useSelector} from 'react-redux'
import {openEntityView} from '../../../redux/actions/exploreActions'
import moment from 'moment'


const GraphView = (props) => {


  const dispatch = useDispatch();
  const themeName = useSelector((state) => state.theme);
  const [loading,setLoading] = useState(false);
  const [networkGraph,setNetworkGraph] = useState();
  console.log(themeName);
  let options
  if(themeName.className === 'theme-light'){
    options =  widget_helper.GraphOptions;
  }else{
    options =  widget_helper.DarkGraphOptions;
  }


  const events = {
    select : function(event) {
      var { nodes, edges } = event;
      console.log(nodes);
      console.log(edges);

      if(nodes.length>0){

        const selectedNode = nodes[0];
        if(networkGraph){
          const node = networkGraph.body.data.nodes._data[nodes[0]];
          if(node){
            if(node.group=== 'hash' || node.group=== 'file' || node.group=== 'process') {
              const data = {
                fieldType:'malware',
                malwareSearchTye:'malwareValue',
                fieldValue:node.name,
                viewType:'alerts,events,loginEvents,loginUserEvents,userAccess,Malware,Process',
                fieldName:node.fieldName
              }
              displayEntityData(data,'node.group');
            }else if(node.group=== 'host'){
              const data = {
                fieldType:'device',
                fieldValue:node.name,
                viewType:'alerts,events,loginEvents,loginUserEvents,userAccess,Malware,Process',
                fieldName:node.fieldName
              }
              displayEntityData(data,'device');
            }else{
              const data = {
                fieldType:node.group,
                fieldValue:node.name,
                viewType:'alerts,events,loginEvents,loginUserEvents,userAccess,Malware,Process',
                fieldName:node.fieldName
              }
              displayEntityData(data,node.group);
            }

          }



        }

      }

   
    },
    doubleClick : function(event) {
      var { nodes, edges } = event;
      console.log(event);
      if(networkGraph){
        const node = networkGraph.body.data.nodes._data[nodes[0]];

        const data = {
          startDate:moment().subtract(30,'days').valueOf(),
          endDate:moment().valueOf(),
          fieldType:node.fieldName,
          fieldValue:node.name,
          indicatoryField:node.name,
          isExpand:"true"
        }
        caseService.expandNode(data).then((responseData) =>{
          let nodes = [];
          let edges = [];
          for(var i=0;i<responseData.data.nodes.length;i++){
            const currentNode = responseData.data.nodes[i];
            if(!networkGraph.body.data.nodes._data[currentNode.id]){
              const singleNode = {
                id:currentNode.id,
                name:currentNode.name,
                label:currentNode.label,
                fieldName:currentNode.fieldName,
                alertName:currentNode.alertName,
                isThreatIntelNode:currentNode.isThreatIntelNode,
                group:currentNode.type,

              }
              nodes.push(singleNode);
            }

          }
          for(var i=0;i<responseData.data.edges.length;i++){
            const currentNode = responseData.data.edges[i];
            const singleNode = {
              from:currentNode.source,
              to:currentNode.target,

            }
            edges.push(singleNode);
          }
          networkGraph.body.data.nodes.add(nodes)
          networkGraph.body.data.edges.add(edges)
        })
      }



    }
  };

 

  const displayEntityData = (data,type) =>{
    setLoading(true);
    caseService.getEntityInfo(data).then((response)=>{
      const entityData = {entityType:type,processData:response.data }
      console.log(entityData);
      dispatch(openEntityView(entityData));
      setLoading(true)
    })
  }

  return (
    <div style = {{width:'100%', height:'100%'}}>


     
      {props.graphData && props.graphData.nodes && <Graph events={events} graph={props.graphData} options={options}  style={{ height: "100%",width:'1200px' }}

      getNetwork={network => {
       setNetworkGraph(network)
       }}
      />}



    </div>
  );
};

export default GraphView;