  export const calculateTimeRange = (startDate,endDate) =>{

    let intreval = "10s";
    let buckets = 100;
    let stepTs = Math.abs(startDate-endDate)/buckets;
    const duration = moment.duration(stepTs);
    const minutesOfDuration = duration.asMinutes();
    const secondsOfDuration = duration.asMilliseconds() / 1000;
    if (minutesOfDuration > 0) {
        intreval = Math.round(minutesOfDuration) + "m";
    } else if (secondsOfDuration > 0) {
        intreval =  Math.round(secondsOfDuration) + "s";
    }
    if(intreval === '0m'){
        intreval = '10s'
    }

    return intreval;

  }
export const calculateTick = (startDate,endDate) =>{

    let intreval = "10";
    let buckets = 100;
    let stepTs = Math.abs(startDate-endDate)/buckets;
    const duration = moment.duration(stepTs);
    const minutesOfDuration = duration.asMinutes();
    const secondsOfDuration = duration.asMilliseconds() / 1000;
    if (minutesOfDuration > 0) {
        intreval = Math.round(minutesOfDuration);
    } else if (secondsOfDuration > 0) {
        intreval =  Math.round(secondsOfDuration);
    }
    if(intreval === '0m'){
        intreval = '10'
    }

    return intreval;

}

