
import React from "react";
import sizeMe from 'react-sizeme'
import {
    Legend,
    Chart,
    Axis,
    Geom,
    Interaction, Annotation

} from "bizcharts";

import { connect,compose } from 'react-redux';
import _ from 'lodash';

import {  CardBody } from 'reactstrap';
import {changeDateSettings} from '../../../redux/actions/panelActions'
import * as widgetHelper from '../../../shared/helpers/widget_helper'
function processTimeSeriesData (data){
  let finalArray = [];
  for(var i=0;i<data.length;i++){
     
      let dataPoints = data[i].datapoints;
      dataPoints.map(function(singlePoint){
        let object = new Object();
        if(singlePoint[0]){
          object['value'] = singlePoint[0];
        }else{
          object['value'] = 0;
        }
        object['label'] = singlePoint[1];
       
        object['metric'] = data[i].target
        finalArray.push(object);
      });
     
  }
  return finalArray;
}



function getComponent(data,config,height,width,dispatch,timeFromat,tickCount) {
 

  let finalData = [];



  if(data.length > 0  && !data[0].rows){
     finalData = processTimeSeriesData(data);
  }
  




  const scale = {
    label: {
      type: "timeCat",
      mask:timeFromat,
      tickCount:tickCount
    },
    value:{
      type: config.scale
    }
    
   
  };

  const triggerEvent = (event,finalData) =>{
      
        
    if(event.filteredData.length!=0 && event.filteredData.length!=finalData.length){

      const dataArray = [];
      for(var i=0;i<event.filteredData.length;i++){
          dataArray.push(event.filteredData[i].label);
      }

      const minValue =  Math.min.apply(Math, dataArray);
      const maxValue =  Math.max.apply(Math, dataArray);
      let object = {strat:minValue,end:maxValue,label:""}
      
      setTimeout(function(){
        dispatch(changeDateSettings(this.strat,this.end,"test"));
      }.bind(object),1000);

    

      //return true;

    }

  }

 



  class RenderChart extends React.Component {


    constructor(props){
      super(props);
     
    }

    getValue  (value){

     let percentvalue = value*0.25

      return value+(percentvalue)
  }


    


    render() {
      
     
      return (
       
        <CardBody style = {{height:'100%'}}>
         
         {finalData.length > 0 && 
         <Chart

         data={finalData}
         height = {height}
         width = {width}
         scale={scale}

         autoFit


         onAfterrender={(e: IEvent, chart: G2Instance) => {
          triggerEvent(chart,finalData)
       }}
       >
         <Axis
           name="label"
           label = {{autoRotate:false}}
         />
         <Axis
           name="value"
           
         />
         <Legend 
         />
         <Geom
           type="line" 
           position="label*value"
           color="metric"
           shape={config.shape}
           size={config.lineWidth}
         />
           {config.thresholds && config.thresholds.map((value,index)=>{
             return (

                 <React.Fragment>

                   <Annotation.Line
                       start={['min', value['from'+index]]}
                       end={['max', value['from'+index]]}
                       style={{
                         fontSize: 12,
                         fontWeight: 'normal',
                         stroke:widgetHelper.ThresholdColors[value['color'+index]],
                         lineWidth:1.2
                       }}
                       text = "text"
                   />
                   <Annotation.Text
                       position={['min', this.getValue(value['from'+index])]}
                       content={value['label'+index]}

                   />
                 </React.Fragment>
                 )
           })}

         <Interaction type = "brush"/>
         
       </Chart>
      }
      {data.length > 0  && data[0].rows &&
          <h4>Unable to Graph Data. Only Time series was supported</h4>
      }
      </CardBody>
       
      );
    }
  }
  return RenderChart;
}

class LineChart extends React.Component {
  render() {
    const { width, height } = this.props.size
    const { timeFromat } = this.props

    const tickCount  = getTickCount(width);
    const finalWidth = width - 30;
    let finalHeight = 300
    if(height !=0){
      finalHeight  = height - 50;
    }

    const {dispatch} = this.props;
    const RenderChart = getComponent(this.props.data,this.props.chartConfig,finalHeight,finalWidth,dispatch,timeFromat,tickCount);
    return (
     
       <RenderChart />
     
    );
  }
}

function getTickCount(width){
    if(width>1000){
        return 10;
    }
    if(width>500 && width<1000){
        return 8
    }
    if(width>500){
        return 5
    }
}
export default connect()(sizeMe({ monitorHeight: true })(LineChart));



