import AutoSizer from 'react-virtualized-auto-sizer';
import AddIcon from 'mdi-react/AddIcon';
import SettingsIcon from 'mdi-react/SettingsIcon';
import AddPanelWidget from '../AddPanelWidget/AddPanelWidget';
import CloseIcon from 'mdi-react/CloseIcon';
import 'react-toastify/dist/ReactToastify.css';
import WidgetPannel from '../AddPanelWidget/WidgetPannel'
import moment from "moment";

interface Props{
    panel:PanelModel
}

class DashboardPanel  extends PureComponent<Props>{
    
    constructor(props) {
        super(props);
        this.state = { data: {data:[]},loading:false };
        
        this.fetchPanelData = this.fetchPanelData.bind(this);
    }

    fetchPanelData = () =>{
        const {panel,filters,date} = this.props;
        const startDate = date[0].startDate.getTime()
        const endDate = date[0].endDate.getTime()
        const finalDate = {startDate:startDate,endDate:endDate};



        this.setState(function(state, props) {
          return {
            loading: true
          };
        });
        let mergedFilters = [];
        if(panel.type && panel.type === 'alerts' ){
          let tempFilters = [];
          for(let i=0;i<filters.length;i++){
            let key = filters[i].key;
            if(!key.startsWith("rule_data.event_data.")){
              tempFilters.push({key:"rule_data.event_data."+filters[i].key,operator:filters[i].operator,value:filters[i].value})
            }else{
              tempFilters.push(filters[i])
            }
          }
          for(let i=0;i<panel.filters.length;i++){
            let key = panel.filters[i].key;
            if(!key.startsWith("rule_data.event_data.")){
              tempFilters.push({key:"rule_data.event_data."+panel.filters[i].key,operator:panel.filters[i].operator,value:panel.filters[i].value})
            }else{
              tempFilters.push(panel.filters[i])
            }
          }
          mergedFilters = tempFilters;
        }else{
          let tempFilters = [];
          for(let i=0;i<filters.length;i++){
            let key = filters[i].key;
            if(key.startsWith("rule_data.event_data.")){
              tempFilters.push({key:filters[i].key.replace("rule_data.event_data.",""),operator:filters[i].operator,value:filters[i].value})
            }else{
              tempFilters.push(filters[i])
            }
          }
          for(let i=0;i<panel.filters.length;i++){
            let key = panel.filters[i].key;
            if(key.startsWith("rule_data.event_data.")){
              tempFilters.push({key:panel.filters[i].key.replace("rule_data.event_data.",""),operator:panel.filters[i].operator,value:panel.filters[i].value})
            }else{
              tempFilters.push(panel.filters[i])
            }
          }
          mergedFilters = tempFilters;
        }
       

        
        
        const queryData = this.getQuery(this.props.panel,finalDate,mergedFilters,this.props.searchString);


        dashboardService.showPanelPreivew(queryData).then((response) => {
            var responses = {responses:[response.data.data]} 
            let finalQuery  =  JSON.parse(panel.query)
            const er = new ElasticResponse(finalQuery, responses);
            
            if(panel.chartConfig.chartType === 'table' && panel.chartConfig.table.showSparkLine!='true'){
              let esResponse =  er.getTimeSeries('table');
              this.setState(function(state, props) {
                return {
                  data: esResponse
                };
              });
            }else{
              let esResponse =  er.getTimeSeries('NA');
              this.setState(function(state, props) {
                return {
                  data: esResponse
                };
              });
            }
           
        });
    }

     getQuery = (panel,date,filters,searchString) =>{
        const queryBuilder = new ElasticQueryBuilder({
            timeField: '@timestamp',
            esVersion: '7.9',
        });
        let queryString = searchString
        let finalQuery  =  JSON.parse(panel.query)
        var query = queryBuilder.build(finalQuery[0],filters,queryString);
        let payload = JSON.stringify(query);
        let duration = widgetHelper.calculateTimeRange( date.startDate,date.endDate);
        console.log(duration);
        let finalData =  payload.replaceAll("$timeFrom", date.startDate);
        let fainlData1 = finalData.replaceAll("$timeTo", date.endDate);
        let finalData2 = fainlData1.replaceAll("$__interval",duration)
        let panelJson = panel;
        panelJson['elsQuery'] = finalData2;
        let data = {'panelData':JSON.stringify(panelJson)}; 

        return data;

    }

    componentDidUpdate(prevProps) {
        // Typical usage (don't forget to compare props):
        
        let isPropsEqual =   _.isEqual(this.props, prevProps);
        if(!isPropsEqual){
            
            this.fetchPanelData();
        }

      }

      shouldComponentUpdate(nextProps, nextState){
        let isPropsEqual =   _.isEqual(this.props.filters, nextProps.filters);
        
        if(this.props.date[0].startDate != nextProps.date[0].startDate || !isPropsEqual || this.props.searchString!=nextProps.searchString ){
            this.setState(function(state, props) {
                return {
                  data: {data:[]}
                };
          });
          return true;
        }else{
            let isPropsEqual1 =   _.isEqual(this.state.data.data, nextState.data.data);
            if(!isPropsEqual1){
                return true;
            }
        }
        return false;
      }



    openSettings = (panel) => {
        
        const {dispatch} = this.props;
        let query = JSON.parse(panel.query)
        panel.query = query;
        dispatch(editPanel(panel))
        dispatch(addCreatedPanel(panel))
    }

    componentDidMount(){
        this.fetchPanelData();
    }

    render() {
       
        const {panel} = this.props;

        return (
           <div style = {{display: 'block',
            height: '100%'}}>

            <Card  style = {{paddingBottom:"0px"}} >
           
              <div className="panel__btns">
                 <button className="panel__btn" type="button" onClick = {this.openSettings.bind(this,panel)}><SettingsIcon /></button>  
                 <button className="panel__btn" type="button" onClick={this.props.deletePanel.bind(this,panel.id)}><CloseIcon /></button>
              </div>
              {this.state.data.data.length>0 &&
                <WidgetPannel panelData = {this.state.data} filters = {this.props.filters} date = {this.props.date} panel = {panel}/>

              }
              
      </Card>


           

           

                  

            </div>
          
           
           
          
        );

    }

}



export default connect()(DashboardPanel);