import React from 'react';
import { Col, Container, Row,ButtonToolbar,Button,Card,CardBody,ButtonGroup } from 'reactstrap';
import _ from "lodash";

import dashboardService from '../../../api/dashboardService'
import * as widgetHelper  from "../../../shared/helpers/widget_helper"

import Client from "../../../events/Events"
import DashboardGrid from "./DashboardGrid"
import { connect } from 'react-redux';
import DashboardModel from '../../../models/DashboardModel';
import PanelModel from '../../../models/PanelModel'
import {openSingleDashboard} from '../../../redux/actions/dashboardActions'
import BarLoader from "react-spinners/BarLoader";




class NewDashboard extends React.Component {


       
  
       constructor(props) {
          super(props);
          const data = {id:0,uid:'',title:'dashboarTitle',time:'',refresh:false,}
          this.state = {
            dashboard:{id:0,title:'',panelModel:[]},
            currentPannel:{},
            fields : [],
            isModelOpen : false,
            dashboardModel : new DashboardModel(data),
            loading:false
          };
          //const layout = this.generateLayout();
          //this.state = { layout };
          this.fetchFields = this.fetchFields.bind(this);
          this.fetchSingleDashboard = this.fetchSingleDashboard.bind(this);
          
         
        }

      fetchFields (){
        dashboardService.getFieldsWithDataTypes() .then((response) => {
          const fields = [];
          for (const [key, value] of Object.entries(response.data)) {
             fields.push({label:key,value:key})
          }
          this.setState({
               fields:fields
           });
       });
      }
      fetchSingleDashboard (id){
        const {dispatch,dashboarData} = this.props;
      this.setState((state) => {
          return {loading: true}
      });

         
          if(dashboarData){
            const existingPermissions = JSON.parse(dashboarData.config.config);
            const existingPanels = dashboarData.panels;

            const tempPanels = [];
             for(var i=0;i<existingPanels.length;i++){
               
               const singlePanel = new PanelModel(existingPanels[i]);
               tempPanels.push(singlePanel)
             }
          
            const data = {
              id:dashboarData.id,
              uid:'',
              title:dashboarData.name,
              time:'',
              refresh:false,
              panels :tempPanels,
              description:dashboarData.description,
              category:dashboarData.category,
              permissions:existingPermissions
            }

            dispatch(openSingleDashboard(data));
            this.setState((state) => {
              return {loading: false}
            });


          }
          
         

      }

      componentDidMount() {
          this.fetchSingleDashboard(this.props.dashboardId);
      }






      
    
          
render() {
  
    return (
        <React.Fragment>
             {this.state.loading && <BarLoader color= "#53c8b7" height ={4} width = "100%"/>}
             {!this.state.loading && <DashboardGrid updateDashboardName = {this.props.updateDashboardName}  fields = {this.state.fields} />}
        </React.Fragment>
      
     
    );
  }
}


  
  export default connect()(NewDashboard)



