import axiosInstance from '../middleware/Interceptor'




function searchData (data) {
    return axiosInstance.post('/siem-core/user/historical/',data)

}
function saveFilters (data){
    return axiosInstance.post('/siem-core/user/historical/save_search',data)
}

function getSavedSearches(){
    return axiosInstance.get("/siem-core/user/historical/get-save-searches")
}

function getThreatHuntingQuries(data){
    return axiosInstance.post("/siem-core/user/historical/get-threat-hunting-quries",data)
}
function saveThreatHuntingQuries(data){
    return axiosInstance.post("/siem-core/user/historical/save-hunting-quries",data)
}

function getThreatHuntingSaveQuries(){
    return axiosInstance.get("/siem-core/user/historical/get-threat-hunting-quries")
}







const historicalService = {
    searchData,
    saveFilters,
    getSavedSearches,
    getThreatHuntingQuries,
    saveThreatHuntingQuries,
    getThreatHuntingSaveQuries

}

export default historicalService