import React,{useEffect, useState} from 'react';

import { useSelector,useDispatch } from 'react-redux';
import { Col,Modal, ModalHeader, ModalBody, CardBody,Button,Card  } from 'reactstrap';
import { Field, reduxForm,SubmissionError,FieldArray } from 'redux-form';
import dashboardService from '../../../api/dashboardService';
import userService from '../../../api/userService';
import _ from 'lodash'
import { ToastContainer, toast } from 'react-toastify';
import renderSelectField from '../../../shared/components/form/Select';
import BarLoader from "react-spinners/BarLoader";
import {closeEditDashboard} from '../../../redux/actions/dashboardActions'
import DashboardModel from '../../../models/DashboardModel';
const permissions = [
    {label:'Viewer',value : 'viewer'},
    {label:'Admin',value : 'admin'},
    {label:'Editor',value : 'Editor'},


]

const EditDashboard = (props) =>{

     const initialValues = {title:'test'}
     const isSidePanelOpen = useSelector((state) => state.dashboards.dashboard.isEditDashboardOpen);

     const { error, handleSubmit, pristine, reset, submitting,initialize,change } = props

     const [categories,setCategories] = useState([]);
     const [users,setUsers] = useState([])
     useEffect(() => {
        fetchPanelData();
        getAccessList();
    }, []);
    
    const  fetchPanelData = () =>{
        
        dashboardService.getDashboardCategories().then((response) => {
            const data = response.data.map(function(value,index){
                return {label:value.name,value:value.id}
            });
            setCategories(data);
        });
    }
    

    const  getAccessList = () =>{
        
        userService.getAccessList().then((response) => {
            const data = response.data.map(function(value,index){
                return {label:value.name,value:value.name}
            });
            setUsers(data);
        });
    }

    if(props.dashboard){
      return (<ShowPanel  
        dashboard = {props.dashboard} 
        isSidePanelOpen = {isSidePanelOpen}
         error  = {error}
         handleSubmit  = {handleSubmit}
         pristine  = {pristine}
         reset  = {reset}
         submitting = {submitting}

         categories = {categories}
         accessList = {users}
         initialize = {initialize}
         change = {change}
         updateDashboardName = {props.updateDashboardName}
        />)
    }else{
      let data = {id:0,title:'',permissions:[]}
      const dashboardModel  = new DashboardModel(data);

      return (<NewDashboard  
        dashboard = {dashboardModel} 
        isSidePanelOpen = {isSidePanelOpen}
         error  = {error}
         handleSubmit  = {handleSubmit}
         pristine  = {pristine}
         reset  = {reset}
         submitting = {submitting}
         categories = {categories}
         accessList = {users}
         initialize = {initialize}
         change = {change}
         updateDashboardName = {props.updateDashboardName}
        />)
    }

  

}

const NewDashboard = (props) => {
  const { dashboard,error, handleSubmit, pristine, reset, submitting,touched,initialize } = props


  const [loading,setLoading] = useState(false);

  const [showNewDashboard,setShowNewDashboard] = useState(false);
  const [dashboardId,setDashboardId] = useState(0);
  const dispatch = useDispatch();

  const changeEventHanlder = (event) =>{
    
    dashboard.updateNonSelectValue(event);
   
  }

  const changeCategory = (event) =>{
    
    dashboard.updateCategory(event);
   
  }

  const updatePermissions = (index,event,name) =>{
    dashboard.updatePermissions(index,event,name)
  }

 


  const addField = (fields) =>{
    fields.push({})
    dashboard.addField();
  }

  const removeField = (fields,index) =>{
    fields.remove(index);
    dashboard.removeField(index);
  }


  const renderPermissions = ({ fields }) => (
     <React.Fragment>


         <Col md = {12}>

         <Button style = {{float:'right'}} color="link" onClick={() => addField(fields)}><span class="lnr lnr-plus-circle"></span></Button>

         </Col>
      
          <table className = "table">

              <thead>
                  <tr>
                      <th>Permission</th>
                      <th>User</th>
                      <th>Action</th>
                  </tr>
              </thead>

              <tbody>
              {fields.map((member, index) =>
                  <tr>
                          <td> 
                              <Field  onChange={(target) => updatePermissions(index,target,'permission')} name={`${member}.permission`} type="select" options = {permissions} component={renderSelectField}/>
                          </td>
                          <td> 
                              <Field onChange={(target) => updatePermissions(index,target,'username')} name={`${member}.username`} options = {props.accessList}  type="text" component={renderSelectField}/>
                          </td>
                          <td>
                              <Button style = {{float:'right'}} color="link" onClick={() => removeField(fields,index)}><span class="lnr lnr-trash"></span></Button>
                          </td>
                  </tr>    
              )}  
              </tbody>
          </table>

       
      </React.Fragment>
    )

  const closeEditModel = () =>{
      dispatch(closeEditDashboard(true))
  }

  const goToNewDashboard =()=>{
    props.location.push("/dashboard/new/"+dashboardId);
  }

  const submit =(values,formValues)=>{
    
       if(!values.title ){
        throw new SubmissionError({
            title: 'Please Enter Valid Title'
           
          })
       }
       if(!values.description ){
        throw new SubmissionError({
            description: 'Please Enter Valid Description'
           
          })
       }
       if(!values.category ){
        throw new SubmissionError({
            category: 'Please Valid Category'
           
          })
       }

       if(values.members.length==0){
        throw new SubmissionError({
            members: 'Permissions are mandatory'
          })
       }
     
       const data = {
         id:0,
         title:values.title,
         description:values.description,
         config:JSON.stringify(values.members),
         categoryId:values.category.value
        };
       
        setLoading(true);

       dashboardService.saveDashboard(data).then((response) => {
          setLoading(false);
           if(response.status === 200){
              setShowNewDashboard(true);
              setDashboardId(response.id);
            toast.success('Configuration was saved successfully', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
           }else{
            toast.error('Unable to Save Configuration', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
           }
      });

      
    }
  return (  <Modal  style = {{maxWidth:800}} size = "lg"  isOpen={props.isSidePanelOpen} >

              {loading && <BarLoader color= "#53c8b7" height ={4} width = {800}/>}
            
            <ModalHeader>New Dashboard</ModalHeader>
            
            
               <ModalBody>
               <ToastContainer
      position="top-right"
      hideProgressBar={false}
      autoClose={false}
      newestOnTop={true}
      closeOnClick={false}
      draggable={false}
      rtl={false}
    />
               <Col md={12} lg={12}>
          <Card>
        <CardBody>
         
          <form className="form form--horizontal" onSubmit={handleSubmit(submit)}>
            <div className="form__form-group">
              <span className="form__form-group-label">Title</span>
              <div className="form__form-group-field">
                <Field
                  name="title"
                  component={renderField}
                  type="text"
                  onChange = {changeEventHanlder}
                />
               
              </div>
            </div>

            <div className="form__form-group">
              <span className="form__form-group-label">Description</span>
              <div className="form__form-group-field">
                <Field
                  name="description"
                  component={renderField}
                  type="text"
                  onChange = {changeEventHanlder}
                />
              </div>
             </div>

             <div className="form__form-group">
              <span className="form__form-group-label">Category</span>
              <div className="form__form-group-field">
              <Field   name = "category" component={renderSelectField} 
                   options={props.categories} onChange = {changeCategory}/>
                   
              </div>
              </div>
              <FieldArray name="members" component={renderPermissions}/>
              
              <hr/>

              <Col md = {12}>
              <Button style = {{float: 'right',marginLeft:'12px'}} color="primary" type="submit"  disabled={submitting}>Submit</Button>
              <Button style = {{float: 'right'}} type="button" onClick = {closeEditModel} >
                Close
              </Button>
                  {showNewDashboard &&  <Button style = {{float: 'right'}} color="primary" type="button" onClick = {goToNewDashboard} >
                      Go To Created Dashboard
                  </Button>}

              </Col>
             
            
          </form>
          </CardBody>
          </Card>
          </Col>

          </ModalBody>
           </Modal> 
       
      )



}


const renderField = ({
    input, placeholder, type, meta: { touched, error },
  }) => (
    <div className="form__form-group-input-wrap">
      <input {...input} placeholder={placeholder} type={type} />
      {touched && error && <span className="form__form-group-error">{error}</span>}
    </div>
  );



const ShowPanel = (props) =>{

    const { dashboard,error, handleSubmit, pristine, reset, submitting,touched,initialize } = props
    
    const [loading,setLoading] = useState(true);

    const [dashboardId,setDashboardId] = useState(0);

    const dispatch = useDispatch();

    const changeEventHanlder = (event) =>{
      
      dashboard.updateNonSelectValue(event);
     
    }

    const changeCategory = (event) =>{
      
      dashboard.updateCategory(event);
     
    }

    const updatePermissions = (index,event,name) =>{
      dashboard.updatePermissions(index,event,name)
    }

    useEffect(() => {

      const category = _.find(props.categories, { value: dashboard.category  });
      if(category){
        setLoading(false);
      }
      initialize({
        title:dashboard.title,
        description:dashboard.description,
        category:category,
        members:dashboard.permissions
        
      })
    }, [dashboard.title]);


    const addField = (fields) =>{
      fields.push({})
      dashboard.addField();
    }

    const removeField = (fields,index) =>{
      fields.remove(index);
      dashboard.removeField(index);
    }


    const renderPermissions = ({ fields }) => (
       <React.Fragment>


           <Col md = {12}>

           <Button style = {{float:'right'}} color="link" onClick={() => addField(fields)}><span class="lnr lnr-plus-circle"></span></Button>

           </Col>
        
            <table className = "table">

                <thead>
                    <tr>
                        <th>Permission</th>
                        <th>User</th>
                        <th>Action</th>
                    </tr>
                </thead>

                <tbody>
                {fields.map((member, index) =>
                    <tr>
                            <td> 
                                <Field  onChange={(target) => updatePermissions(index,target,'permission')} name={`${member}.permission`} type="select" options = {permissions} component={renderSelectField}/>
                            </td>
                            <td> 
                                <Field onChange={(target) => updatePermissions(index,target,'username')} name={`${member}.username`} options = {props.accessList}  type="text" component={renderSelectField}/>
                            </td>
                            <td>
                                <Button style = {{float:'right'}} color="link" onClick={() => removeField(fields,index)}><span class="lnr lnr-trash"></span></Button>
                            </td>
                    </tr>    
                )}  
                </tbody>
            </table>

         
        </React.Fragment>
      )

    const closeEditModel = () =>{
        dispatch(closeEditDashboard(true))
    }

    const submit =(values,formValues)=>{
      
         if(!dashboard.title ){
          throw new SubmissionError({
              title: 'Please Enter Valid Title'
             
            })
         }
         if(!dashboard.description ){
          throw new SubmissionError({
              description: 'Please Enter Valid Description'
             
            })
         }
         if(!dashboard.category ){
          throw new SubmissionError({
              category: 'Please Valid Category'
             
            })
         }
  
         if(dashboard.permissions.length==0){
          throw new SubmissionError({
              members: 'Permissions are mandatory'
            })
         }
       
         const data = {
           id:dashboard.id,
           title:values.title,
           description:values.description,
           config:JSON.stringify(dashboard.permissions),
           categoryId:dashboard.category
          };
         
          setLoading(true);

         dashboardService.saveDashboard(data).then((response) => {
            setLoading(false);
             if(response.status === 201){

               let object = new Object();
               object['id'] = dashboard.id;
               object['label'] = dashboard.title;
               props.updateDashboardName(object);
              toast.success('Configuration was saved successfully', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
             }else{
              toast.error('Unable to Save Configuration', {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
              });
             }
        });
  
        
      }
    return (  <Modal  style = {{maxWidth:800}} size = "lg"  isOpen={props.isSidePanelOpen} >

                {loading && <BarLoader color= "#53c8b7" height ={4} width = {800}/>}
              
              <ModalHeader>Edit Dashboard</ModalHeader>
              
              
                 <ModalBody>
                 <ToastContainer
        position="top-right"
        hideProgressBar={false}
        autoClose={false}
        newestOnTop={true}
        closeOnClick={false}
        draggable={false}
        rtl={false}
      />
                 <Col md={12} lg={12}>
            <Card>
          <CardBody>
           
            <form className="form form--horizontal" onSubmit={handleSubmit(submit)}>
              <div className="form__form-group">
                <span className="form__form-group-label">Title</span>
                <div className="form__form-group-field">
                  <Field
                    name="title"
                    component={renderField}
                    type="text"
                    onChange = {changeEventHanlder}
                  />
                 
                </div>
              </div>

              <div className="form__form-group">
                <span className="form__form-group-label">Description</span>
                <div className="form__form-group-field">
                  <Field
                    name="description"
                    component={renderField}
                    type="text"
                    onChange = {changeEventHanlder}
                  />
                </div>
               </div>

               <div className="form__form-group">
                <span className="form__form-group-label">Category</span>
                <div className="form__form-group-field">
                <Field   name = "category" component={renderSelectField} 
                     options={props.categories} onChange = {changeCategory}/>
                     
                </div>
                </div>
                <FieldArray name="members" component={renderPermissions}/>
                
                <hr/>

                <Col md = {12}>
                <Button style = {{float: 'right',marginLeft:'12px'}} color="primary" type="submit"  disabled={submitting}>Submit</Button>
                <Button style = {{float: 'right'}} type="button" onClick = {closeEditModel} >
                  Close
                </Button>
                </Col>
               
              
            </form>
            </CardBody>
            </Card>
            </Col>

            </ModalBody>
             </Modal> 
         
        )



}




export default reduxForm({
  form: 'edit_dashboard_form',
  enableReinitialize: true, 
})(EditDashboard)




  

