import React,{useEffect,useState} from 'react';
import caseService from '../../../api/caseService'
import dashboardService from '../../../api/dashboardService'
import { AgGridReact, AgGridColumn,ChangeDetectionStrategyType } from '@ag-grid-community/react';
import { ServerSideRowModelModule } from '@ag-grid-enterprise/server-side-row-model';
import { RowGroupingModule } from '@ag-grid-enterprise/row-grouping';
import { MenuModule } from '@ag-grid-enterprise/menu';
import { ColumnsToolPanelModule } from '@ag-grid-enterprise/column-tool-panel';
import { AllModules } from '@ag-grid-enterprise/all-modules';

import { ClientSideRowModelModule } from '@ag-grid-community/client-side-row-model';

const EventsView = (props) =>{

    console.log("click")
    const {alertDetails} = props;
    const [eventData,setEventData] = useState([]);
    const [fields,setFields] = useState([]);
    const [gridApi, setGridApi] = useState(null);
    const [gridColumnApi, setGridColumnApi] = useState(null);
    const onGridReady = (params) => {
        setGridApi(params.api);
        setGridColumnApi(params.columnApi);

        var columnToolPanel = params.api.getToolPanelInstance('columns');
        columnToolPanel.setPivotModeSectionVisible(false);
        columnToolPanel.setRowGroupsSectionVisible(false);
        columnToolPanel.setValuesSectionVisible(false);
        params.api.closeToolPanel();
        
       
    }

   

    const getColDefs = (fields) =>{
     
     const fieldsNames = [];
     const columns = [];

     //const entites = alertDetails.entities;

     columns.push({headerName:"@timestamp", field: "@timestamp", width: 200,});
    columns.push({headerName:"name", field: "name",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
				filterOptions:['contains'],suppressAndOrCondition:true }});
    columns.push({headerName:"event_name", field: "event_category",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
				filterOptions:['contains'],suppressAndOrCondition:true }});
    columns.push({headerName:"event_category", field: "log_type",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
				filterOptions:['contains'],suppressAndOrCondition:true }});
    columns.push({headerName:"log_device", field: "log_device",width: 200,enableRowGroup: true,filter: 'agTextColumnFilter',filterParams:{
                filterOptions:['contains'],suppressAndOrCondition:true }});

               

    for(var i=0;i<fields.length;i++){
        let field = fields[i];

       

        if(!fieldsNames.includes(field.fieldName)){
        fieldsNames.push(field.fieldName);
         columns.push({
                headerName:field.fieldName, 
                field:field.fieldName,
                hide:true,
                width:150,
                filter: 'agTextColumnFilter',
                enableRowGroup: true
            })
        }
       
    }
    
    setFields(columns);
}

const getRowData = (data)=>{
    const finalData = [];
    let alertData = data;
    if(alertData.rule_data){
        let eventData = alertData.rule_data.event_data
        for(var i=0;i<eventData.length;i++){
            eventData[i]['name'] = alertData.name
            eventData[i]['@timestamp'] = alertData['@timestamp']
            finalData.push(eventData[i]);
        }
        setEventData(finalData);
    }
   
}

const getDefaultColDef = () =>{
    const def = {
        flex: 1,
        minWidth: 100,
        filter: false,
        sortable: true,
        resizable: true,
        floatingFilter: true
    }
      return def;
}


    useEffect(() => {
        
        const fetchData = async () => {

            dashboardService.getAllLogFields().then((response) => {

                if(response.status === 200){
                    if(alertDetails.type === 'case'){
                        caseService.getSingleAlert(alertDetails.alertId).then((alertResponse) => {
                            getRowData(alertResponse.data);

                            getColDefs(response.data);
                        });
                    }else if(alertDetails && alertDetails.rule_data){

                        getRowData(alertDetails);
                        getColDefs(response.data);
                    }

                }
               
            });    


               
        };
        fetchData();       
    },[alertDetails.id])
    if(eventData.length>0){
      
        return ( <div
        
            id="myGrid"
            style={{
              height: '100%',
              width: '100%',
            }}
            className="ag-theme-alpine"
          >
              <AgGridReact
        rowHeight={30}
        rowSelection={'single'}
        modules={[ClientSideRowModelModule,RowGroupingModule,MenuModule,ColumnsToolPanelModule]}
        
        sideBar={{
            toolPanels: [
              {
                id: 'columns',
                labelDefault: 'Columns',
                labelKey: 'columns',
                iconKey: 'columns',
                toolPanel: 'agColumnsToolPanel',
                toolPanelParams: {
                  suppressRowGroups: true,
                  suppressValues: true,
                  suppressPivots: true,
                  suppressPivotMode: true,
                  suppressSideButtons: true,
                  suppressColumnFilter: true,
                  suppressColumnSelectAll: true,
                  suppressColumnExpandAll: true,
                },
              },
            ],
            defaultToolPanel: 'columns',
          }}
        sideBar={true}
       
        columnDefs={fields}
        pivotMode={false}
        rowGroupPanelShow= {'always'}
        defaultColDef={getDefaultColDef()}
        
        animateRows={true}
        onGridReady={onGridReady}
         rowData={eventData}
        
      />
        </div> );
      
        
    }

    return ( <div
        
        id="myGrid"
        style={{
          height: '100%',
          width: '100%',
        }}
        className="ag-theme-alpine"
      >
    </div> );

}

export default EventsView