import React,{useState,useEffect} from 'react';
import SplitterLayout from 'react-splitter-layout';
import '../../../../node_modules/react-splitter-layout/lib/index.css';
import CasesSidePanel from '../../Cases/components/CasesSidePanel'
import CaseAlert from '../../Cases/components/CaseAlert'
import CaseEntityView from '../../Cases/components/CaseEntityView'

import dashboardService  from '../../../api/dashboardService'
import userService from '../../../api/userService'
const CaseLayout = (props) =>{

  const [fields,setFields] = useState([]);
  const [users,setUsers] = useState([]);

  const {caseId} = props;

  useEffect(() => {
    fetchAllFields();
   
  }, [])

  const fetchAllFields = () =>{
    dashboardService.getFieldsWithDataTypes().then((response) => {
        if(response.status === 200){
            fetchUserDetails(response.data);
        }

  });
  }

    const fetchUserDetails = (cases) =>{
        userService.getAccessList().then((response) => {
            const tempUsers = [];
            tempUsers.push({name:'System'});
            for(var i=0;i<response.data.length;i++){
                if(response.data[i].type === 'user'){
                    tempUsers.push(response.data[i]);
                }

            }
            setUsers(tempUsers);
            setFields(cases);
        });
    }
  

return ( 
<div style = {{paddingTop:'50px'}}>

<SplitterLayout primaryIndex={1}  primaryMinSize={20} secondaryInitialSize={250}>
           <CasesSidePanel caseId = {props.caseId} users = {users}/>
           <SplitterLayout secondaryInitialSize={250}>
             <SplitterLayout vertical secondaryInitialSize={250}>
                <CaseAlert users = {users} />

             </SplitterLayout>
                <CaseEntityView fields = {fields} />
           </SplitterLayout>
         </SplitterLayout>
</div>

     )

}

export default CaseLayout;