class DashboardLayout extends React.PureComponent{
    constructor(props){
        super(props)
		
		this.state =  {
        	currenId:"",
			model: FlexLayout.Model.fromJson(json),
			dashboardData:{}
        }
		
		this.addDashboardToLayout = this.addDashboardToLayout.bind(this);
		this.onAction = this.onAction.bind(this);
	}

	onAction = (action) => {


		console.log(this.state.model.toJson());
		if(action.type === "FlexLayout_SelectTab"){
			let id = action.data.tabNode;
			if(this.state.currenId != id ){
				let existingDashbaords1 = this.state.model.toJson();
				let existingDashbaords = existingDashbaords1['layout']['children'][0]['children'];
				for(var i=0;i<existingDashbaords.length;i++){
					if(existingDashbaords[i].id === id){

						let jsonData = {dashboardId:id.replace("#",""), layout:JSON.stringify(this.state.model.toJson())};
						dashboardService.saveGridLayout(jsonData).then(response => {

						})

						dashboardService.getSingleDashboard(id.replace("#","")).then((responseData) =>{
							this.setState((state, props) => ({
								currenId:id,
								dashboardData:responseData.data
							}));
						});

					}
				}
			}
			return action;

		}
		if(action.type === "FlexLayout_DeleteTab"){

			let id = action.data.node;
			let existingDashbaords = this.state.model.toJson();
			for(let i=0;i<existingDashbaords.layout.children[0].children.length;i++){
				const currentId = existingDashbaords.layout.children[0].children[i].id;
				if(currentId === id){
					existingDashbaords.layout.children[0].children.splice(i,1);
					if(existingDashbaords.layout.children[0].children!=0){
						let currentId1 = existingDashbaords.layout.children[0].children[existingDashbaords.layout.children[0].children.length-1].id
						let jsonData = {dashboardId:currentId1.replace("#",""),layout:JSON.stringify(existingDashbaords)};
						dashboardService.saveGridLayout(jsonData).then(response => {

						})
					}

					return action;
				}
			}


		}
		return action;
       
    }


	
	
	addDashboardToLayout =(event)=>{



    	//this.refs.layout.addTabToActiveTabSet(event);
		let existingDashbaords = this.state.model.toJson();
		existingDashbaords['layout']['children'][0]['children'].push(event);

		const model = FlexLayout.Model.fromJson(existingDashbaords);

		let jsonData = {dashboardId:event.id.replace("#",""),layout:JSON.stringify(existingDashbaords)};



		dashboardService.saveGridLayout(jsonData).then(response => {

		})

		this.setState((state, props) => ({
			model: model,
			currenId:event.id
		}));



		
	}

	

	updateDashboardName =(event)=>{
			const json = this.state.model.toJson();
			for(var i=0;i<json['layout']['children'][0]['children'].length;i++){
				if(json['layout']['children'][0]['children'][i].id === "#"+event.id){
					json['layout']['children'][0]['children'][i].name = event.label;
				}
			}


			dashboardService.saveGridLayout(json).then(response => {

			})
		
		
	}



    componentDidMount(){
		const {dispatch} = this.props;

		const { id } = this.props.match.params

        dashboardService.getAllLogFields().then(response => {
			if(response.status === 200){
				this.fetchDashboardLayout(response.data,id);
			}
		})


    }

    updateDashboardLayout = (payload) =>{

		for(let i=0;i<payload.borders.length;i++){
			if(payload.borders[i].location === 'left'){
				for(let j=0;j<payload.borders[i].children.length;j++){
					payload.borders[i].children[j]['show'] = false;
				}

			}

		}
		return payload;
	}

    fetchDashboardLayout = (fields,id) =>{

		dashboardService.getGridLayout().then(response => {
			if(response.data && response.data.layout){

				let payload = JSON.parse(response.data.layout);
				 payload = this.updateDashboardLayout(payload);

				 this.fetchSingleDashboard(fields,payload,id,response.data.dashboardId);


			}

		})
	}

	fetchSingleDashboard = (fields,payload,id,layoutDashboardId) =>{
    	const {dispatch} = this.props;
    	dashboardService.getSingleDashboard(id).then((responseData) =>{
			this.setState((state) => {
				return {fields: fields}
			});
			const finalDashboardId = "#"+id;
			let isFound  = false;
			for(let i=0;i<payload.layout.children[0].children.length;i++){

					if(payload.layout.children[0].children[i].id === finalDashboardId){
						isFound = true;
					}

			}
			if(!isFound){
				const currentDashboard = {
					"type": "tab",
					"id": finalDashboardId,
					"name":responseData.data.name,
					"component": "dashboard"
				}
				payload['layout']['children'][0]['children'].push(currentDashboard);
			}

			const model = FlexLayout.Model.fromJson(payload);
			this.setState((state, props) => ({
				fields:fields,
				model: model,
				currenId:"#"+id,
				dashboardData:responseData.data
			}));
			if(id){
				model.doAction(Actions.selectTab("#"+id));
			}else{
				model.doAction(Actions.selectTab("#"+layoutDashboardId));
			}

			let params = queryString.parse(this.props.location.search)
			if(params && params.query && params.startDate && params.endDate){

				let filters = JSON.parse(params.query);
				let date = {startDate:params.startDate,endDate:params.endDate}
				dispatch(applyBulkFilters(filters,date));
			}

		})

	}

    factory = (node) => {
		const {dispatch} = this.props;
		var component = node.getComponent();
		
		
        if (component === "dashboard") {

			const id = node._attributes.id;
			console.log(id+">>>>>"+ this.state.currenId)
			if(node._attributes.id === this.state.currenId){
				//dispatch(addNewDashboard(id.replace("#","")))
				return <NewDashboard dashboarData = {this.state.dashboardData}  />;
			}
			
        }
        if (component === "logs") {
            return (<LogViewer fields = {this.state.fields}/>)
		}
		else if (component === "sub") {
			
            var model = node.getExtraData().model;
            if (model == null) {
                node.getExtraData().model = FlexLayout.Model.fromJson(node.getConfig().model);
                model = node.getExtraData().model;
                // save submodel on save event
                
            }

            return <FlexLayout.Layout model={model} factory={this.factory} />;
        }else if(  component === 'eventlogviewer'){
			return (<LogViewer type = "events" isRender = {this.state.renderSub}   fields = {this.state.fields}/>);
		}else if(component === 'alertlogviewer'){
			return (<LogViewer type = "alerts" isRender = {this.state.renderSub}   fields = {this.state.fields}/>);
		}

		else if(component === 'eventviewer'){
			return (<LogDetailView/>);
			
		}else if (component === 'dashboards'){
			return (<DashboardTree addDashboardToLayout = {this.addDashboardToLayout}/>);
		}
    }
    render() {

		console.log(this.state.model)
		//
        return (
			<FlexLayout.Layout
				ref="layout"
			model={this.state.model}
			onAction={this.onAction}
			factory={this.factory}/>
        )
    }
}


