const Filter = (props) =>{
    let {fields,type} = props;
    const currentData = useSelector((state) => state.historicalReducer.historicalModel.date);
    const [modelData,setModelData] = useState({isOpen:false,viewType:'save'});
    useEffect(() => {
        if(currentData.length!=0){
            const lable = moment(currentData[0].startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(currentData[0].endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
            setLabel(lable)
        }

    },[currentData]);


    const [label,setLabel] = useState("")
    let {finalSearchString} = props ;
    const dispatch = useDispatch();

   if(type === 'alerts'){
       let tempFields = [];
       for(let i=0;i<fields.length;i++){
           tempFields.push({name:"rule_data.event_data."+fields[i].name,type:'string',enumerations:null})
       }
       fields = tempFields;
   }





    const [dropdownOpen, setDropdownOpen] = useState(false);
    const [timeRangeDropDown, setTimeRangeDropDown] = useState(false);
    const toggle = () => setDropdownOpen(prevState => !prevState);
    const toggle1 = () => setTimeRangeDropDown(prevState => !prevState);
    const [indexType,setIndexType] = useState("Events")
    const openModel = (view) =>{
        let data= {isOpen:true,viewType:view}
        setModelData(data)
    }
    const changeDateHandler =  (start, end,label)=>{

        const lable = moment(start.selection.startDate.getTime()).format('MM/DD/YYYY h:mm:ss') +" to "+moment(start.selection.endDate.getTime()).format('MM/DD/YYYY h:mm:ss')
        setLabel(lable)
        dispatch(applyFilter(start));
    }

    const searchString = (query) =>{
        dispatch(applySearchString(query));
    }

    const closePanel = () =>{
        let data= {isOpen:false,viewType:modelData.viewType}
        setModelData(data)
    }
    const setType = (type) =>{
        setIndexType(type)
        dispatch(applyIndexType(type))
    }



    return (
        <div className="header-container">
            <Row>
                <Col xs={6} sm={6} lg={6} md={6} >

                    <Assist
                        placeholder='Search Logs   F'
                        defaultValue={finalSearchString}
                        onSubmit={query => searchString(query)}
                        data={fields}
                        inputProps={inputProps}
                        dropdownProps={dropdownProps}
                        selectorProps={selectorProps}
                    />



                </Col>
                <Col xs={3} sm={3} lg={3} md={3} >



                    <ButtonGroup>
                        <Dropdown isOpen={dropdownOpen} toggle={toggle}>
                            <DropdownToggle caret>
                                {indexType}
                            </DropdownToggle>
                            <DropdownMenu>
                                <DropdownItem onClick = {()=>setType('Raw')}>Raw</DropdownItem>
                                <DropdownItem onClick = {()=>setType('Events')}>Events</DropdownItem>
                                <DropdownItem onClick = {()=>setType('Alerts')}>Alerts</DropdownItem>

                            </DropdownMenu>
                        </Dropdown>

                        <Button color="link" onClick={()=>openModel('save')}>Save</Button>
                        <Button color="link" onClick={()=>openModel('open')}>Open</Button>
                    </ButtonGroup>
                </Col>

                <Col xs={2} sm={2} lg={2} md={2} >
                    <Dropdown isOpen={timeRangeDropDown} toggle={toggle1}>
                        <DropdownToggle caret>
                            {label}
                        </DropdownToggle>
                        <DropdownMenu style = {{width:"600px",marginLeft: '-131%',top:'35px'}}>
                            <DateRangePicker
                                onChange={(item,label) => changeDateHandler(item,label)}
                                showSelectionPreview={true}
                                moveRangeOnFirstSelection={false}

                                ranges={currentData}
                                direction="vertical"
                                scroll={{ enabled: true }}
                                staticRanges={defaultStaticRanges}
                            />;
                        </DropdownMenu>
                    </Dropdown>
                </Col>


            </Row>
            <SaveFilter close = {closePanel} isSidePanelOpen = {modelData.isOpen} viewType = {modelData.viewType}/>
        </div>

    )

}

