import {
    ADD_PANNEL,
    UPDATE_PANEL,
    OPEN_SIDE_BAR,
    CLOSE_SIDE_BAR,
    CREATE_PANEL,
    UPDATE_CHART_CONFIG,
    UPDATE_CHART_TYPE,
    CHANGE_AGGS,
    UPDATE_QUERY,
    UPDATE_SETTINGS,
    SHOW_WIDGETS_PANEL,
    OPEN_EDIT_DASHBOARD,
    CLOSE_EDIT_DASHBOARD,
    ADD_CREATED_PANEL,
    OPEN_SIGNLE_DAHSBOARD,
    ADD_CREATED_PANEL_TO_DASHBOARD,
    EDIT_PANEL,
    CHANGE_DATE_SETTINGS,
    APPLY_SEARCH,
    ADD_NEW_DASHBOARD,
    DELETE_PANEL
  } from '../actions/dashboardActions';
  import _ from 'lodash';
  import cloneDeep from 'lodash/cloneDeep';
  import DashboardModel from '../../models/DashboardModel'
  import * as queryHelper from '../../shared/helpers/query_helper'
  import PanelModel from '../../models/PanelModel'
  import moment from 'moment';
import {subHours} from "date-fns";
  const data = {id:0,uid:'',title:'dashboarTitle',time:'',refresh:false,isSidePanelOpen:false,panels:[],isEditDashboardOpen:false,currentPanelId:0}
  const dashboardModel = new DashboardModel(data )

  const initialState = {
    dashboard:dashboardModel,
      date: [
          {
              startDate: subHours(new Date(),24 ),
              endDate: new Date(),
              key: 'selection'
          }
      ],
    filters : [],
    id:0,
    searchString :'*'
  };
  const dashboardReducer = (state = initialState, action) => {

    if(action.type === ADD_CREATED_PANEL_TO_DASHBOARD){
      const currentDashboard = cloneDeep(state.dashboard);
      const panel = new PanelModel(action.payload);
      currentDashboard.panels.push(panel);
      return Object.assign({}, state, { dashboard: currentDashboard  });
    }
    if(action.type === ADD_NEW_DASHBOARD){
     
      return Object.assign({}, state, { id: action.id  });
    }

    if(action.type === APPLY_SEARCH){
     
      return Object.assign({}, state, { searchString: action.searchString  });
    }
    if(action.type === DELETE_PANEL){
        const currentDashboard = cloneDeep(state.dashboard);
        let panels = currentDashboard.panels;
        for(var i=0;i<panels.length;i++){
            if(panels[i].id === action.payload){
                currentDashboard.panels.splice(i,1);
            }
        }

        return Object.assign({}, state, { dashboard: currentDashboard  });
    }
    if(action.type === 'APPLY_FILTERS'){
      const currentFilters = cloneDeep(state.filters);
      currentFilters.push(action.payload);
     return Object.assign({}, state, { filters: currentFilters  });
    }
    if(action.type === 'APPLY_BULK_FILTERS'){
        const currentFilters = cloneDeep(state.filters);
        const filters = action.payload;
        for(var i =0;i<filters.length;i++){
            currentFilters.push(filters[i]);
        }
        console.log(action.date)
        const startDate = new Date(parseInt(action.date.startDate));
        const endDate = new Date(parseInt(action.date.endDate));

       const finalDateObject = {
            startDate: startDate,
            endDate: endDate,
            key: 'selection'
        }

        //return Object.assign({}, state, { date: [action.start.selection]  });

        return Object.assign({}, state, { filters: currentFilters,date:[finalDateObject]  });
    }
    if(action.type === EDIT_PANEL){
      const dashboard = cloneDeep(state.dashboard);
      dashboard.isSidePanelOpen = true;
      
      return Object.assign({}, state, { dashboard: dashboard  });
    }

    if(action.type === 'DELETE_FILTERS'){
      const revmoedFilters = cloneDeep(state.filters);
      revmoedFilters.splice(action.index,1);
       return Object.assign({}, state, { filters: revmoedFilters  });
    }
    
    

   if(action.type === OPEN_SIGNLE_DAHSBOARD){
        const tempDashboardModel =  new DashboardModel(action.payload )
        return Object.assign({}, state, { dashboard: tempDashboardModel });
    }


    if(action.type === CREATE_PANEL){
      const dashboard = cloneDeep(state.dashboard);
      dashboard.showCreatePanel = true;
      dashboard.panels = action.dashboard.panels;
      dashboard.currentPanel = action.dashboard.currentPanel;
      return {...state,dashboard:dashboard};
    }

    if(action.type === CHANGE_DATE_SETTINGS){

     return Object.assign({}, state, { date: [action.start.selection]  });
    }  
  
    
    
    

     

     
     //CHANGE_AGGS

   
    

    if(action.type === OPEN_SIDE_BAR){

      const dashboard = cloneDeep(state.dashboard);
      dashboard.isSidePanelOpen = true;
      
      return {...state,dashboard:dashboard};
    }
    if(action.type === SHOW_WIDGETS_PANEL){

      const dashboard = cloneDeep(state.dashboard);
      dashboard.showCreatePanel = false;
      
      return {...state,dashboard:dashboard};
    }
    
    if(action.type === CLOSE_SIDE_BAR){

      const dashboard = state.dashboard;
      dashboard.isSidePanelOpen = false;
      return {...state,dashboard:dashboard};
    }
    if(action.type === OPEN_EDIT_DASHBOARD){
      const dashboard = state.dashboard;
      dashboard.isEditDashboardOpen = !dashboard.isEditDashboardOpen;
      return {...state,dashboard:dashboard};
    }
    if(action.type === CLOSE_EDIT_DASHBOARD){
      const dashboard = state.dashboard;
      dashboard.isEditDashboardOpen = false;
      return {...state,dashboard:dashboard};
    }


  
    


   return state;
  };
  export default dashboardReducer;
  