import React from 'react'
import {Row, Col, Card, CardBody} from 'reactstrap'
const EntityType = (props) =>{

    const {entity} = props

    return (
        <div className="explore-entity-view">
            <div className="left-sidebar__header">

                {entity && entity.type === 'User' &&
                     <UserTypeEntity/>
                }
                {entity && entity.type === 'Device' &&
                 <DeviceTypeEntity entity = {entity}/>
                }
                {entity && entity.type === 'IP' &&
                    <IPTypeEntity entity = {entity}/>
                }


           </div>
        </div>

    );
}

