import React from 'react';
import SplitterLayout from 'react-splitter-layout';
import queryString from 'query-string'

import AlertView from '../Explore/components/AlertView'
import GraphView from '../Explore/components/GraphView'

import caseService from '../../api/caseService'
import EventsView from '../Explore/components/EventsView'
import EntityView from '../Explore/components/EntityView'
import { connect } from 'react-redux';
import {openAlert} from '../../redux/actions/exploreActions'
class Explore extends React.Component {



    constructor(props) {
        super(props);
        this.state = { alerts: [],graphData : {},alertDetails:{} };
        this.highlightNodes = this.highlightNodes.bind(this);
    }

      fetchSingleCase = () => {
          const {dispatch} = this.props;
         const values =  queryString.parse(this.props.location.search)
         const id = values.id;
         const investigationId = values.investigationId;
         if(investigationId){
             caseService.getInvestigationById(investigationId).then((response) => {
                 const data = response.data;
                 data['type'] = "investigation"
                 this.setState((state, props) => ({
                     alerts: response.data
                 }));



             });
         }else{
             caseService.getSingleCase(id).then((response) => {
                 const data = response.data;
                 data['type'] = "case"
                 this.setState((state, props) => ({
                     alerts: response.data
                 }));
                 if(response.data.alertEvidences){
                     let payloayd = {alertDetails:response.data.alertEvidences[0],caseDetails:response.data};
                     dispatch(openAlert(payloayd))
                     this.setState((state, props) => ({
                         alertDetails: response.data.alertEvidences[0]
                     }));
                 }

             });
         }

      }

     fetchGrapData = () => {
              const values =  queryString.parse(this.props.location.search)
              const id = values.id;
              let type = "case";
                const investigationId = values.investigationId;
                let finalId = values.id;
                if(investigationId){
                    type = "alert"
                    finalId = investigationId;
                }
                if(investigationId){
                    caseService.getGraphDataByInvestigationName(finalId,type).then((response) => {
                        var tempNodes =[];
                        const nodes  = response.data.nodes;
                        for(var i=0;i<nodes.length;i++){
                            if(nodes[i].isThreatIntelNode){
                                nodes[i]['color'] = {border:'red'};
                                nodes[i]['borderWidth'] = 3;

                            }
                            tempNodes.push(nodes[i]);
                        }
                        const finalData = {nodes:tempNodes,edges:response.data.edges}
                        this.setState((state, props) => ({
                            graphData: finalData
                        }));
                    });
                }else{
                    caseService.getGraphData(finalId,type).then((response) => {
                        var tempNodes =[];
                        const nodes  = response.data.nodes;
                        for(var i=0;i<nodes.length;i++){
                            if(nodes[i].isThreatIntelNode){
                                nodes[i]['color'] = {border:'red'};
                                nodes[i]['borderWidth'] = 3;

                            }
                            tempNodes.push(nodes[i]);
                        }
                        const finalData = {nodes:tempNodes,edges:response.data.edges}
                        this.setState((state, props) => ({
                            graphData: finalData
                        }));
                    });
                }

        }

    componentDidMount() {

            this.fetchSingleCase();
            this.fetchGrapData();
    }

    highlightNodes = (alertDetails) =>{
        const {dispatch} = this.props;
        this.setState((state, props) => ({
            alertDetails:alertDetails
        }));
        let payloayd = {alertDetails:alertDetails,caseDetails:this.state.alerts};
        dispatch(openAlert(payloayd))
         
    }


    render() {
        return (
        <div style = {{paddingTop:'50px'}}>

<SplitterLayout primaryIndex={1} secondaryInitialSize={250}>
        <AlertView highlightNodes = {this.highlightNodes} alertData = {this.state.alerts}/>
      <SplitterLayout secondaryInitialSize={250}>
        <SplitterLayout vertical secondaryInitialSize={250}>
          <GraphView graphData = {this.state.graphData}/>
            <SplitterLayout secondaryInitialSize={250}>
            <EventsView alertDetails = {this.state.alertDetails}/>
            
          </SplitterLayout>
        </SplitterLayout>
          <EntityView/>
        
      </SplitterLayout>
    </SplitterLayout>


         </div>
        )
    }

}

export default connect()(Explore)
