import React,{useState,useEffect} from 'react';
import {
    Card,
      CardBody, Nav, NavItem, NavLink, TabContent, TabPane,CardTitle,Col,Row, Modal, ModalHeader, ModalBody, ModalFooter,Button

  } from 'reactstrap';


import '../../../../node_modules/react-splitter-layout/lib/index.css';
import Moment from 'react-moment';
import moment from 'moment'
import { useDispatch, useSelector } from 'react-redux';
import parse from 'html-react-parser';
import {openEvidence} from '../../../redux/actions/casesAction'
import userService from '../../../api/userService'
import caseService from '../../../api/caseService'
import classnames from 'classnames';
import {openCase} from '../../../redux/actions/casesAction'
import { Chart, Point } from 'bizcharts';
import TextEditor from '../../../shared/components/text-editor/TextEditor';
const CaseAlert = (props) =>{

    const caseDetails = useSelector((state) => state.cases.caseDetails);
    const {users} = props;

    return (<React.Fragment>

        <CaseHeader users = {users} caseDetails = {caseDetails}/>
        <CaseBody caseDetails = {caseDetails}/>

    </React.Fragment>)

}

const CaseHeader = (props) =>{


  const {caseDetails,users} = props;
  const [assignedUser,setAssignedUser] = useState("")
  const [caseState,setCaseState] = useState("")
  const [casePr,setCasePr] = useState("")

  const dispatch = useDispatch();

  console.log(users)

  const [isModelOpen,setIsModelOpen] = useState(false);

  const openExternalCase = () =>{
      console.log("cete")
      setIsModelOpen(!isModelOpen)
  }

  const changeAssigee = (event) =>{
      const value = event.target.value;
      let data = {userName:event.target.value,id:caseDetails.id}
      caseService.changeAssinee(data).then((response) => {
         openCurrentCase()
      }); 
  } 
  const changeCaseState = (event) =>{
    const value = event.target.value;
    let data = {status:event.target.value,id:caseDetails.id}
    caseService.changeCaseState(data).then((response) => {
      openCurrentCase()
    }); 
  }
  
  const changePr = (event) =>{
    const value = event.target.value;
    let data = {priority:event.target.value,id:caseDetails.id}
    caseService.changePr(data).then((response) => {
      openCurrentCase()
    }); 
  }
  
  const assignToMe = (event) =>{
  
    let data = {userName:event.target.value,id:caseDetails.id}
    caseService.assingToMe(data).then((response) => {
      if(response.data.status){
        setAssignedUser(response.data.username);
      }
      
    }); 

    

} 



const openCurrentCase = () =>{
        
  caseService.getSingleCase(caseDetails.id) .then((response) => {
      if(response.status===200 && response.data){
          dispatch(openCase(response.data));
      }
   })

  

}



  useEffect(() => {
    if(caseDetails && caseDetails.assignedUser){
       setAssignedUser( caseDetails.assignedUser);
       setCaseState(caseDetails.status)
       setCasePr(caseDetails.priority)
    }



  }, [])




    
    
    return ( 

        <div className = "page-content__center--content">
            <div className =  {`page-content__header--left case__status--${caseDetails.priority}`} >

            <div className = "page-content__header cases-details-header">
                    <div className = {`case__status case-card__status`}></div>

                    <div className = "case-header-assignee">
                        <div className = "ob-avatar__container">
                            <div className = "user-image">
                                { caseDetails.assignedUser && <span>{caseDetails.assignedUser.substring(0, 2).toLocaleUpperCase()}</span>}
                            </div>
                        </div>

                    </div>
                    <div className = "information-container">
                        <select value={ caseDetails.assignedUser} onChange = {changeAssigee} className = "form-control" name = "assignedUser"  style = {{width:'100px',    border: "0px"}}>
                           
                           {users && users.length>0 &&

                            users.map((value, index) => {

                            return (<option value = {value.name}>{value.name}</option>)

                          })
                           
                      }

                        </select>
                        <button onClick = {assignToMe} class="assign-me-btn action-naked-button simp-btn simp-btn__color--naked simp-btn__size--small simp-btn__shape--rectangle ng-star-inserted" color="naked" size="small" smpbutton="" tnautomationid="assignToMeButton" aria-disabled="false" tabindex="0">Assing to me</button>
                    </div>
            </div>
            </div>
           
            <div className = "middle.two-columns">

                <div className = "description-case-automation">

                    <span style ={{fontWeight:'bold'}}>{caseDetails.title}</span>  | ID  {caseDetails.caseId} | Modified {caseDetails.createDate} | Status: {caseDetails.status} 
                   <Row>
                   <Col xs="3">
                     <select  onChange = {changePr} value = {caseDetails.priority}  className = "form-control" name = "caseStatus"  style = {{width:'100px',    border: "0px",paddingTop:'6px'}}>
                       <option value = "critical">Critical</option>
                       <option value = "high">High</option>
                       <option value = "medium">Medium</option>
                       <option value = "low">Low</option>
                    </select>
                    </Col>
                    <Col xs="3">
                    <select onChange = {changeCaseState}  value = {caseDetails.status} className = "form-control" name = "caseState"  style = {{width:'100px',    border: "0px",paddingTop:'6px'}}>
                       <option value = "Open">Open</option>
                       <option value = "In progress">In Progress</option>
                       <option value = "Closed">Close</option>
                       <option value = "Resolved">Resolve</option>
                    </select>
                    </Col>


                           {!caseDetails.externalCaseId &&
                           <Col xs="3">
                                <button className="buttonContainer"  onClick={openExternalCase}>Create Ticket</button>
                           </Col>
                           }

                           {caseDetails.externalCaseId &&
                           <Col xs="3" style = {{marginTop:'7px'}}>
                              <span>External</span> : <a   href={caseDetails.externalRef} target="_blank"> {caseDetails.externalCaseId}</a>
                           </Col>
                           }



                     
                   </Row>
                  
                 
                </div>

             
               

            </div>

            

            <div className = "page-content__header--right">
                <button className = "buttonContainer" onClick = {openCurrentCase}><span class="lnr lnr-sync"></span></button>
               
               
                
                <a href={`/explore?id=${caseDetails.id}`} target={"_blank"}><img style={{height:'20px',width:'34px',marginBottom:'7px'}} src={"data:image/svg+xml;utf8;base64,"}/> </a>
            </div>

            <ExternalConnectionsModal openCurrentCase = {openCurrentCase} caseId={caseDetails.id}  isPanelModel={isModelOpen} closeModel = {openExternalCase}/>
        </div>


     )
}

const AlertCard = (props) =>{
    const {alertData} = props
     
    return (<Col onClick={ () => props.openEntityView(alertData) } xs="2" sm="2" className = "alertEvidenceCard">
            <Card  className = {`alert-card-container alert-card-container-${alertData.alertPriority}`}>
                <CardBody className = 'alert-card-body-container'>
                    <CardTitle className = "alert-card__content__name" ><span className = "u-ellipsis">{alertData.alertName}</span></CardTitle>
                    <CardTitle  className = "alert-card__content_sub"  ><Moment format="MM/DD/YYYY HH:mm:ss">{alertData.createdDate}</Moment></CardTitle>
                    
                    <CardTitle  className = "alert-card__content_sub">{alertData.eventCount} Events</CardTitle>
                </CardBody>
            </Card>
    </Col>);

}

const CasesTab = (props) =>{
    const [activeTab,setActiveTab] = useState('1');
    const [open,setOpen] = useState(false);
    const [comment,setComment] = useState("");

    const dispatch = useDispatch();

    const openEntityView = (alertData) =>{
      dispatch(openEvidence(alertData))
    }

    const {caseDetails} = props;



    const toggle = (tab) =>{
        if (activeTab !== tab) {
            setActiveTab(tab)
        }
    
    }

    const onRichTextEditChange = (event)=> {
        setComment(event);
    }

    const addComment = () =>{
        if(caseDetails){
            const  caseData = {id:caseDetails.id,note:comment}
            caseService.addNotesToCase(caseData).then((response)=>{
                openCurrentCase();
            })
        }

    }

    const openCurrentCase = () =>{

        caseService.getSingleCase(caseDetails.id) .then((response) => {
            if(response.status===200 && response.data){
                dispatch(openCase(response.data));
            }
        })



    }

    const chartData = [];
    if(caseDetails && caseDetails.alertEvidences){
        openEntityView(caseDetails.alertEvidences[0]);
      for(var i=0;i<caseDetails.alertEvidences.length;i++){
        let object = new Object();
        object['date'] = moment(caseDetails.alertEvidences[i].createdDate).format('HH:mm:ss');
        object['count'] = caseDetails.alertEvidences[i].eventCount
        chartData.push(object);
      }
    }

    const openUpload = () =>{
        setOpen(!open);
    }

    return (<div className="tabs tabs--bordered-bottom">
            <Model close = {openUpload} openCurrentCase = {openCurrentCase} caseId = {caseDetails.id} open = {open}/>
                <div className="tabs__wrap"> 
                <Nav tabs>
                  <NavItem>
                    <NavLink
                      className={classnames({ active: activeTab === '1' })}
                      onClick={() => {
                        toggle('1');
                      }}
                    >
                      Overview
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={classnames({ active: activeTab === '2' })}
                      onClick={() => {
                        toggle('2');
                      }}
                    >
                      History
                    </NavLink>
                  </NavItem>


                    <NavItem>
                        <NavLink
                            className={classnames({ active: activeTab === '4' })}
                            onClick={() => {
                                toggle('4');
                            }}
                        >
                            Evidence
                        </NavLink>
                    </NavItem>
              
                </Nav>
                <TabContent activeTab={activeTab}>
                  <TabPane tabId="1">
                      <div className = "alertContainer">
                         <CardTitle className = "alert-title"><span class="lnr lnr-flag"></span> Alerts</CardTitle>

                      
                      
                      {caseDetails && caseDetails.alertEvidences && 

                          <Row>
                            {caseDetails.alertEvidences.map((value, index) => {

                               return (<AlertCard alertData = {value} openEntityView  = {openEntityView}/>)
 
                            })}

                    

                          </Row>  
                        
                            
                          
                       
                      }
                      </div>

                      <div className = "chart">
                      <Chart padding={[20, 30]} height={100} autoFit data={chartData} >
                         <Point position="date*count"  />
                      </Chart>
                      </div>
                  </TabPane>
                  <TabPane tabId="2">
                  <div className = "wall-case-container__case-comments">
                  {caseDetails && caseDetails.caseActivities && caseDetails.caseActivities.map((value, index) => {

                      return <div className = "case-wall-list--item">
                           <div className = "activity-icon-container">
                           <div className = "smp-icon-size--small">
                           <svg width="100%" height="100%" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill="currentColor"><path d="M19 4.3H5a1.4 1.4 0 00-1.4 1.4v9.1A1.4 1.4 0 005 16.2h2.1v4.2l5.6-4.2H19a1.4 1.4 0 001.4-1.4V5.7A1.4 1.4 0 0019 4.3z"></path></svg>
                           </div>
                           </div>
                          <div className = "action-comment-container">
                           <div className = "item-comment-container">  
                                 <p class="u-word-break--word u-white-space--pre-wrap item-comment item-comment--text underline-links font-regular-primary show-more ng-star-inserted" dir="ltr">{value.activity}</p>
                           </div>
                          </div>

                          <div className = "user-timestamp-container">
                               <div className = "activity-user-box">
                                  <div class="user"><p class="user--name" smptooltipposition="left">System</p></div>
                                 <div class="time"> <Moment format="MM/DD/YYYY HH:mm:ss">{value.activityDate}</Moment></div>
                               </div>
                          </div>
                          
                       </div>
                    
                 
                    })}
                     </div>
                     

                  </TabPane>

                  <TabPane tabId="4">

                      <Card>
                          <CardBody>

                              <div style={{height:'300px',overflowY:'auto'}}>
                                    {caseDetails.notes && caseDetails.notes.map((value, index) => {
                                       return (
                                           <Row style = {{borderBottom:'1px solid #efe3e3'}}>
                                                <Col xs={8} style = {{marginBottom:'12px'}}>
                                                    <p style={{paddingTop:'12px'}}>{parse(value.notes)} </p>
                                                </Col>

                                               <Col xs={4}>
                                                   <p  style={{paddingTop:'12px'}} className="todo__due-date">Created By <b>{value.createdBy}</b></p>
                                               </Col>

                                           </Row>
                                           )
                                    })}
                                  {caseDetails.files && caseDetails.files.map((value, index) => {
                                      return (
                                          <Row style = {{borderBottom:'1px solid #efe3e3'}}>
                                              <Col xs={8} style = {{marginBottom:'12px'}}>
                                                  <p style={{paddingTop:'12px'}}><a href = {`http://192.168.1.57:8899/user/case/downloadFile/${value.fileName}`} target={"_blank"}>{value.fileName}</a> was uploaded by <b>{value.fileUploadedBy}</b> on <Moment format="DD/MM/YYYY HH:mm:ss"><b>{value.fileCreatedDate}</b></Moment> </p>
                                              </Col>



                                          </Row>
                                      )
                                  })}

                              </div>
                              <div style={{marginTop:'10px'}}>
                                  <TextEditor onChange={onRichTextEditChange} />
                              </div>

                          </CardBody>



                      </Card>
                      <Button onClick={addComment} color = "primary" style = {{float: "right",
                          marginRight: '10px'}}>
                          Comment
                      </Button>

                      <Button onClick={openUpload} color = "primary" style = {{float: "right",
                          marginRight: '10px'}}>
                          Upload
                      </Button>





                  </TabPane>
                </TabContent>
                </div>
            </div>         
    );

    
}

const ExternalConnectionsModal = (props) =>{

    const {isPanelModel,closeModel,caseId,openCurrentCase} = props;
    const [externalConnections,setExternalConnections] = useState([])
    const [currentConnectionName,setCurrentConnectionName] = useState("")
    useEffect(()=>{
        fetchExternalCases();
    },[isPanelModel])

    const fetchExternalCases =() =>{
        caseService.getExternalConnections().then((responseData) =>{
            setExternalConnections(responseData.data);
        })
    }

    const submitData = ()=>{
       const data = {caseId:caseId,externalId:currentConnectionName.toString()};
       if(caseId && caseId!= '' && currentConnectionName && currentConnectionName!=''){
           caseService.createCaseInExternal(data).then((responseData) =>{
               if(responseData.status === 200){
                   console.log("created");
                   props.closeModel();
                   props.openCurrentCase();
               }
           })
       }

    }

    return (<Modal isOpen={isPanelModel}  >
        <ModalHeader>Create Ticket in External</ModalHeader>
        <ModalBody>
            <select className="form-control" onChange={(event)=> setCurrentConnectionName(event.target.value)} >
                {externalConnections.map((value)=> {
                    return (<option value={value.id}>{value.connectionName}</option>)
                })}


            </select>
        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick={submitData}>Create Ticket</Button>{' '}
            <Button color="secondary">Cancel</Button>
        </ModalFooter>
    </Modal>);

}

const Model = (props) =>{

    const {caseId,close,openCurrentCase} = props;
    const [file,setFile] = useState();
    const onFileChange = (event) =>{
        setFile(event.target.files[0])
    }
    const uploadFile = ()=>{
        const formData = new FormData();

        // Update the formData object
        formData.append(
            "file",
            file

        );




        caseService.addFileToCase(caseId,formData).then((response) => {
            if(response.status === 200){
                props.close();
                props.openCurrentCase();
            }
        });

    }

    return (<Modal isOpen={props.open}  >
        <ModalHeader>Upload File</ModalHeader>
        <ModalBody>
            <input type="file" onChange={onFileChange} />
        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick={uploadFile}>Upload</Button>{' '}
            <Button color="secondary">Cancel</Button>
        </ModalFooter>
    </Modal>);

}

const CaseBody = (props) =>{
    const {caseDetails} = props;
    return (
    
    <div className = "main-content__left" >
        <CasesTab caseDetails = {caseDetails}/>



        <div className = "case-cli-cnt">

        </div>
    </div>
    
    )
}

export default CaseAlert;